import { PipeTransform, Pipe } from '@angular/core';
import { Customer } from './customer';

@Pipe({
  name: 'customerFilter'
})
export class CustomerFilterPipe implements PipeTransform {

  transform(value: Customer[], filterBy: string): Customer[] {
    filterBy = filterBy ? filterBy.toLocaleLowerCase() : null;
    return filterBy ? value.filter((customer: Customer) =>
      (customer.type == "Private" && customer.firstName.toLocaleLowerCase().indexOf(filterBy) !== -1)
      || (customer.type == "Private" && customer.lastName.toLocaleLowerCase().indexOf(filterBy) !== -1)
      || (customer.type == "Business" && customer.companyName.toLocaleLowerCase().indexOf(filterBy) !== -1)
      || customer.customerNo.toLocaleLowerCase().indexOf(filterBy) !== -1
      || customer.phone.toLocaleLowerCase().indexOf(filterBy) !== -1) : value;
  }
}
