import { Component, OnInit, OnDestroy, Input, Directive } from '@angular/core';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../services/index';
import { Subscription } from 'rxjs/Subscription';
import { ScriptService } from '../script.service';
import { User } from './user';
//import { CustomerService } from './customer.service';
import { DataService } from '../data.service';

declare var $: any;

@Component({
  templateUrl: '/user.search.component.html',
})
export class UserSearchComponent implements OnInit {
  pageTitle: string = 'Search User';
  userSearch: string = "";
  currentProgress: number = 0;
  scriptService: ScriptService = new ScriptService();

  isLoading: boolean = false;

  public usersList: User[] = [];

  constructor(private dataService: DataService,
      public toastr: ToastrService,
    private authenticationService: AuthenticationService,
    private router: Router,
    vcr: ViewContainerRef) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  getUsers() {

    this.isLoading = true;

    this.dataService.getUsers(this.userSearch)
      .subscribe((response: User[]) => {
        this.usersList = response;
        this.isLoading = false;
      },
      (err: any) => console.log(err),
      () => console.log('getUsers() retrieved portal users'));

    //this.usersList = this.dataService.getUsers();

    console.log(this.usersList);
  }


  ngOnInit(): void {
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/users/user-search";

    this.getUsers();

    this.dataService.menuUnselect();
    $("#menuSearchUsers").parent().parent().addClass("open");
    $("#menuSearchUsers").parent().show();
    $("#menuSearchUsers").addClass("active");
  }

  editUser(user: User) {

    this.dataService.setSelectedUser(user);

  }

  delete(userId) {
    event.preventDefault();
    this.dataService.deleteUser(userId, this.authenticationService.loggedUser$.id)
      .subscribe((status: boolean) => {
        if (status) {
          this.toastr.success('The user was successfully deleted!', 'Success!');
          // this.router.navigate(['/customer/customer-search']);

          var currentIndex: number = 0;
          for (let user of this.usersList) {

            if (user.id == userId)
              this.usersList.splice(currentIndex, 1);

            currentIndex++;
          }

        }
        else {
          this.toastr.error('Unable to delete user!', 'Error!');
        }
      },
      (err: any) => this.toastr.error(err.json().message));
  }

}


