import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CalendarModule } from 'primeng/primeng';
import { InvoiceSearchComponent } from './invoice.search.component';
import { InvoiceComponent } from './invoice.component';
import { BillingCustomerFilterPipe } from './billing-customer-filter.pipe';
import { PrintInvoiceComponent } from './print.invoice.component';
import { AuthGuard } from '../guards/auth.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';
import { SharedModule } from '../shared/shared.module';

@NgModule({
  imports: [

    BrowserModule,
    HttpModule,
    CommonModule,
    FormsModule,
    BrowserAnimationsModule,
    CalendarModule,
    SharedModule,
    RouterModule.forRoot([
      {
        path: 'billing',
        component: SiteLayoutComponent,
        children: [
          { path: 'invoice-search', component: InvoiceSearchComponent, canActivate: [AuthGuard] },
          { path: 'invoice/:id', component: InvoiceComponent, canActivate: [AuthGuard] },
          { path: 'invoice', component: InvoiceComponent, canActivate: [AuthGuard] },
          { path: 'print-invoice', component: PrintInvoiceComponent, canActivate: [AuthGuard] }
        ]
      }

    ])
  ],
  declarations: [
    //CustomerComponent,
    InvoiceSearchComponent,
    InvoiceComponent,
    BillingCustomerFilterPipe,
    PrintInvoiceComponent
  ],
  providers: [
  ]
})
export class BillingModule { }

