import { Component, OnInit, OnDestroy, Input, Directive } from '@angular/core';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SearchCriteria } from '../shared/search.criteria';
import { Subscription } from 'rxjs/Subscription';
import { ScriptService } from '../script.service';
import { Contract } from './contract';
import { ContractService } from './contract.service';
import { DataService } from '../data.service';
import { IContractResults } from '../shared/interfaces';
import { PagerService } from '../shared/pager.service';
import { environment } from '../../environments/environment';

declare var $: any;

@Component({
  templateUrl: '/contract.search.component.html',
})
export class ContractSearchComponent implements OnInit {
  pageTitle: string = 'Contract Detail';

  currentProgress: number = 0;
  scriptService: ScriptService = new ScriptService();
  public searchCriteria: SearchCriteria = new SearchCriteria();
  public simpleSearchCriteria: SearchCriteria = new SearchCriteria();
  deleteMessageEnabled: boolean;
  public contractsList: Contract[] = [];
  totalRecords: number = 0;
  page: number = 1;
  isFirstLoad: boolean = true;
  isLoading: boolean = false;
  contractSearch: string = "";

  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];

  constructor(private dataService: DataService,
    private router: Router,
    public toastr: ToastrService,
    vcr: ViewContainerRef,
    private pagerService: PagerService) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  setPage(page: number, searchCriteria: SearchCriteria) {

    if (searchCriteria == null && this.simpleSearchCriteria.contractCriteria.firstName != null && this.simpleSearchCriteria.contractCriteria.firstName != "")
      searchCriteria = this.simpleSearchCriteria;
    else
      searchCriteria = this.searchCriteria;

    searchCriteria.pageSize = environment.pageSize;

    if (!this.isFirstLoad) {
      this.getContractsPage(page, searchCriteria);
    }

    // get pager object from service

    this.pager = this.pagerService.getPager(this.totalRecords, page, searchCriteria.pageSize);
  }

  getContracts() {
    //this.customersList = this.dataService.getCustomers();
    this.dataService.getContracts(this.searchCriteria)
      .subscribe((response: Contract[]) => {
        this.contractsList = response;

      },
      (err: any) => console.log(err),
      () => console.log('getContractsPage() retrieved contracts'));
  }

  pageChanged(page: number) {

    if (this.simpleSearchCriteria.contractCriteria.firstName != "")
      this.getContractsPage(page, this.simpleSearchCriteria);
    else
      this.getContractsPage(page, this.searchCriteria);

  }

  getContractsPage(page: number, searchCriteria: SearchCriteria) {

    this.isLoading = true;

    searchCriteria.pageNumber = page;

    this.dataService.getContractsPage(searchCriteria)
      .subscribe((response: IContractResults) => {
        
        this.contractsList = response.results;
        console.log(this.contractsList);
        this.totalRecords = response.totalRecords;
        if (this.isFirstLoad) {

          this.dataService.cacheContractSearch(searchCriteria, this.contractsList, this.totalRecords);

          this.setPage(1, searchCriteria);
          this.isFirstLoad = false;
        }

        this.isLoading = false;
      },
      (err: any) => console.log(err),
      () => console.log('getContractsPage retrieved contracts'));
  }

  searchContracts() {

    this.searchCriteria = new SearchCriteria();
    this.simpleSearchCriteria.pageSize = environment.pageSize;

    this.isFirstLoad = true;
    this.simpleSearchCriteria.contractCriteria.searchText = this.contractSearch;

    if (this.contractSearch.indexOf(' ') > 0)
    {
      this.simpleSearchCriteria.contractCriteria.firstName = this.contractSearch.split(" ")[0];
      this.simpleSearchCriteria.contractCriteria.lastName = this.contractSearch.split(" ")[1];
      this.simpleSearchCriteria.contractCriteria.searchText = "";
    }

    this.getContractsPage(1, this.simpleSearchCriteria);

    //this.dataService.cacheCustomerSearch(this.searchCriteria, this.customersList);
  }

  searchAdvanceContracts() {

    this.simpleSearchCriteria = new SearchCriteria();
    this.contractSearch = "";
    this.searchCriteria.pageSize = environment.pageSize;

    this.isFirstLoad = true;

    if (this.searchCriteria.contractCriteria.firstName) {
      if (this.searchCriteria.contractCriteria.firstName.indexOf(' ') < 0) {
        this.searchCriteria.contractCriteria.lastName = this.searchCriteria.contractCriteria.firstName;
      }
      else {
        var first: string = this.searchCriteria.contractCriteria.firstName.split(" ")[0];
        var last: string = this.searchCriteria.contractCriteria.firstName.split(" ")[1];
        this.searchCriteria.contractCriteria.firstName = first;
        this.searchCriteria.contractCriteria.lastName = last;
      }
    }
    else {
      this.searchCriteria.contractCriteria.lastName = "";
    }

    this.getContractsPage(1, this.searchCriteria);

    //this.dataService.cacheCustomerSearch(this.searchCriteria, this.customersList);
  }
  editContract(contract: Contract) {

    this.dataService.setSelectedContract(contract);

  }

  delete(contractId) {
    event.preventDefault();

    this.dataService.deleteContract(contractId)
      .subscribe((status: boolean) => {
        if (status) {
          this.toastr.success('The contract was successfully deleted!', 'Success!');
          // this.router.navigate(['/customer/customer-search']);

          var currentIndex: number = 0;
          for (let contract of this.contractsList) {

            if (contract.id == contractId)
              this.contractsList.splice(currentIndex, 1);

            currentIndex++;
          }

        }
        else {
          this.toastr.error('Unable to delete contract!', 'Error!');
        }
      },
      (err: any) => this.toastr.error(err.json().message));
  }


  ngOnInit(): void {
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/contract/contract-search";

    this.searchCriteria.pageSize = environment.pageSize;
    this.simpleSearchCriteria.pageSize = environment.pageSize;

    this.dataService.menuUnselect();
    $("#menuContractSearch").parent().parent().addClass("open");
    $("#menuContractSearch").parent().show();
    $("#menuContractSearch").addClass("active");

    if (this.dataService.contractsList$.length > 0) {
      this.isFirstLoad = true;

      this.searchCriteria = this.dataService.searchCriteria$;
      this.contractsList = this.dataService.contractsList$;

    }

  }

}

