    getInvoicesPage(searchCriteria: SearchCriteria): Observable<IInvoiceResults> {
        var url: string = this.serverUrl + '/api/invoices/list';

        return this.http.post(url, searchCriteria)
            .map((res: Response) => {
                //const totalRecords = +res.headers.get('x-inlinecount');
                let invoices = res.json().invoices;
                var totalRows = 0;
                if (invoices.length > 0) {
                    totalRows = invoices[0].totalRows;
                }

                var results: IInvoiceResults = {
                    results: invoices,
                    totalRecords: totalRows
                };

                return results;
            })
            .catch(this.handleError);
    }

