    getUserActivity(userId: number, count: number, fromCreateDate: Date, toCreateDate: Date, activityType: string): Observable<UserActivity[]> {

        var criteria: SearchCriteria = new SearchCriteria();
        criteria.userId = userId;
        criteria.pageSize = count;
        if (fromCreateDate != undefined)
            criteria.fromInvoiceDate = fromCreateDate;
        criteria.activityType = activityType;
        if (toCreateDate != undefined)
            criteria.toInvoiceDate = toCreateDate;

        var url: string = this.serverUrl + '/api/useractivity/list';

        return this.http.post(url, criteria)
            .map((res: Response) => {

                let useractivities = res.json();

                return useractivities;
            })
            .catch(this.handleError);
    }

