import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from '../services/index';
import { DataService } from '../data.service';
import { CustomerStatistics } from './customer.statistic';

declare var AmCharts: any;
declare var moment: any;
declare var $: any;

@Component({

  templateUrl: '/pre.paid.component.html'
})
export class PrePaidComponent {
  pageTitle: string = 'Pre-Paid Subscribers';
  newSubsribers: number;
  public customerStatisticsList: CustomerStatistics[] = [];

  constructor(private dataService: DataService,
    private router: Router,
    public toastr: ToastrService,
    private authenticationService: AuthenticationService,
    vcr: ViewContainerRef) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  getSubscribersCount() {

    this.dataService.getCustomersStatistics(this.authenticationService.loggedUser$.id)
      .subscribe((response: CustomerStatistics[]) => {
        console.log(response);
        this.customerStatisticsList = response;
        var stat = this.customerStatisticsList[0];
        
        this.newSubsribers = stat.count;
       
        $("#newSubsribersSpan").attr("data-value", stat.count);
        $("#currentMounthBonus").attr("data-value", stat.count * 10);

        $("[data-counter='counterup']").counterUp({
          delay: 10,
          time: 1000
        });

        (<any>AmCharts.makeChart("dashboard_amchart_pre_paid", {
          "type": "serial",
          "addClassNames": true,
          "theme": "light",
          "path": "/assets/global/plugins/amcharts/ammap/images/",
          "autoMargins": false,
          "marginLeft": 50,
          "marginRight": 8,
          "marginTop": 10,
          "marginBottom": 26,
          "balloon": {
            "adjustBorderColor": false,
            "horizontalPadding": 10,
            "verticalPadding": 8,
            "color": "#ffffff"
          },

          "dataProvider": [{
            "month": this.customerStatisticsList[5].mounth,
            "bonus": this.customerStatisticsList[5].count * 10,
            "subscribers": this.customerStatisticsList[5].count
          }, {
              "month": this.customerStatisticsList[4].mounth,
              "bonus": this.customerStatisticsList[4].count * 10,
            "subscribers": this.customerStatisticsList[4].count
          }, {
              "month": this.customerStatisticsList[3].mounth,
              "bonus": this.customerStatisticsList[3].count * 10,
            "subscribers": this.customerStatisticsList[3].count
          }, {
              "month": this.customerStatisticsList[2].mounth,
              "bonus": this.customerStatisticsList[2].count * 10,
            "subscribers": this.customerStatisticsList[2].count
          }, {
              "month": this.customerStatisticsList[1].mounth,
              "bonus": this.customerStatisticsList[1].count * 10,
            "subscribers": this.customerStatisticsList[1].count,
          }, {
              "month": this.customerStatisticsList[0].mounth,
              "bonus": this.customerStatisticsList[0].count * 10,
            "subscribers": this.customerStatisticsList[0].count,
            "dashLengthColumn": 5,
            "alpha": 0.2,
            "additional": "(projection)"
          }],
          "valueAxes": [{
            "axisAlpha": 0,
            "position": "left"
          }],
          "startDuration": 1,
          "graphs": [{
            "alphaField": "alpha",
            "balloonText": "<span style='font-size:12px;'>[[title]] in [[category]]:<br><span style='font-size:20px;'>[[value]]</span> [[additional]]</span>",
            "fillAlphas": 1,
            "title": "Bonus",
            "type": "column",
            "valueField": "bonus",
            "dashLengthField": "dashLengthColumn"
          }, {
            "id": "graph2",
            "balloonText": "<span style='font-size:12px;'>[[title]] in [[category]]:<br><span style='font-size:20px;'>[[value]]</span> [[additional]]</span>",
            "bullet": "round",
            "lineThickness": 3,
            "bulletSize": 7,
            "bulletBorderAlpha": 1,
            "bulletColor": "#FFFFFF",
            "useLineColorForBulletBorder": true,
            "bulletBorderThickness": 3,
            "fillAlphas": 0,
            "lineAlpha": 1,
            "title": "Subscribers",
            "valueField": "subscribers"
          }],
          "categoryField": "month",
          "categoryAxis": {
            "gridPosition": "start",
            "axisAlpha": 0,
            "tickLength": 0
          },
          "export": {
            "enabled": true
          }
        }));

      },
      (err: any) => console.log(err),
      () => console.log('getCustomersCountByMonth() retrieved count'));
  }

  ngOnInit(): void {

    this.getSubscribersCount();

    // Handles counterup plugin wrapper


    $('.easy-pie-chart .number.transactions').easyPieChart({
      animate: 1000,
      size: 75,
      lineWidth: 3,
      barColor: 'yellow'
    });

    $('.easy-pie-chart .number.visits').easyPieChart({
      animate: 1000,
      size: 75,
      lineWidth: 3,
      barColor: 'green'
    });

    $('.easy-pie-chart .number.bounce').easyPieChart({
      animate: 1000,
      size: 75,
      lineWidth: 3,
      barColor: 'red'
    });

    $('.easy-pie-chart-reload').click(function () {
      $('.easy-pie-chart .number').each(function () {
        var newValue = Math.floor(100 * Math.random());
        $(this).data('easyPieChart').update(newValue);
        $('span', this).text(newValue);
      });
    });

   
    (<any>AmCharts.makeChart("dashboard_amchart_post_paid", {
      "type": "serial",
      "addClassNames": true,
      "theme": "light",
      "path": "/assets/global/plugins/amcharts/ammap/images/",
      "autoMargins": false,
      "marginLeft": 50,
      "marginRight": 8,
      "marginTop": 10,
      "marginBottom": 26,
      "balloon": {
        "adjustBorderColor": false,
        "horizontalPadding": 10,
        "verticalPadding": 8,
        "color": "#ffffff"
      },

      "dataProvider": [{
        "month": "March",
        "bonus": 75,
        "subscribers": 120
      }, {
        "month": "April",
        "bonus": 95,
        "subscribers": 135
      }, {
        "month": "May",
        "bonus": 60,
        "subscribers": 110
      }, {
        "month": "June",
        "bonus": 120,
        "subscribers": 140
      }, {
        "month": "July",
        "bonus": 172,
        "subscribers": 160,
      }, {
        "month": "August",
        "bonus": 220,
        "subscribers": 215,
        "dashLengthColumn": 5,
        "alpha": 0.2,
        "additional": "(projection)"
      }],
      "valueAxes": [{
        "axisAlpha": 0,
        "position": "left"
      }],
      "startDuration": 1,
      "graphs": [{
        "alphaField": "alpha",
        "balloonText": "<span style='font-size:12px;'>[[title]] in [[category]]:<br><span style='font-size:20px;'>[[value]]</span> [[additional]]</span>",
        "fillAlphas": 1,
        "title": "Bonus",
        "type": "column",
        "valueField": "bonus",
        "dashLengthField": "dashLengthColumn"
      }, {
        "id": "graph2",
        "balloonText": "<span style='font-size:12px;'>[[title]] in [[category]]:<br><span style='font-size:20px;'>[[value]]</span> [[additional]]</span>",
        "bullet": "round",
        "lineThickness": 3,
        "bulletSize": 7,
        "bulletBorderAlpha": 1,
        "bulletColor": "#FFFFFF",
        "useLineColorForBulletBorder": true,
        "bulletBorderThickness": 3,
        "fillAlphas": 0,
        "lineAlpha": 1,
        "title": "Subscribers",
        "valueField": "subscribers"
      }],
      "categoryField": "month",
      "categoryAxis": {
        "gridPosition": "start",
        "axisAlpha": 0,
        "tickLength": 0
      },
      "export": {
        "enabled": true
      }
    }));

    $('#dashboard-report-range').daterangepicker({
      "ranges": {
        'Today': [moment(), moment()],
        'Yesterday': [moment().subtract('days', 1), moment().subtract('days', 1)],
        'Last 7 Days': [moment().subtract('days', 6), moment()],
        'Last 30 Days': [moment().subtract('days', 29), moment()],
        'This Month': [moment().startOf('month'), moment().endOf('month')],
        'Last Month': [moment().subtract('month', 1).startOf('month'), moment().subtract('month', 1).endOf('month')]
      },
      "locale": {
        "format": "MM/DD/YYYY",
        "separator": " - ",
        "applyLabel": "Apply",
        "cancelLabel": "Cancel",
        "fromLabel": "From",
        "toLabel": "To",
        "customRangeLabel": "Custom",
        "daysOfWeek": [
          "Su",
          "Mo",
          "Tu",
          "We",
          "Th",
          "Fr",
          "Sa"
        ],
        "monthNames": [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December"
        ],
        "firstDay": 1
      },
      //"startDate": "11/08/2015",
      //"endDate": "11/14/2015",
      opens: 'left',
    }, function (start, end, label) {
      $('#dashboard-report-range span').html(start.format('MMMM D, YYYY') + ' - ' + end.format('MMMM D, YYYY'));
    });

    $('#dashboard-report-range span').html(moment().subtract('days', 29).format('MMMM D, YYYY') + ' - ' + moment().format('MMMM D, YYYY'));
    $('#dashboard-report-range').show();

    $(".amcharts-chart-div").find("a").hide();
  }
}
