import { Component, OnInit, OnDestroy, Input, Directive } from '@angular/core';
import { DataService } from '../data.service';
import { ScriptService } from '../script.service';
import { Router, ActivatedRoute } from '@angular/router';
import { UserActivity } from './user.activity';
import { User } from '../users/user';
import { Customer } from '../customers/customer';
import { AuthenticationService } from '../services/index';
import { MenuItem } from 'primeng/primeng';
import { CustomerStatistics } from '../subscribers/customer.statistic';

declare var $: any;
@Component({
  selector: 'app-welcome',
  templateUrl: '/welcome.component.html'
})

export class WelcomeComponent implements OnInit {
  public pageTitle: string = 'Welcome';
  scriptService: ScriptService = new ScriptService();
  isLoading: boolean = false;
  public usersList: User[] = [];
  public currentUser: User;
  public recordsCount: number = 10;
  public activityType: string = '';

  public userActivitiesList: UserActivity[] = [];
  public customerStatisticsList: CustomerStatistics[] = [];
  public fromCreateDate: Date;
  public toCreateDate: Date;

  constructor(private dataService: DataService, private authenticationService: AuthenticationService, private router: Router) {

  }
  getUserActivities(userId, records, fromCreateDate, toCreateDate, activityType) {

    this.isLoading = true;

    this.dataService.getUserActivity(userId, records, fromCreateDate, toCreateDate, activityType)
      .subscribe((response: UserActivity[]) => {
        this.userActivitiesList = response;
        this.isLoading = false;
      },
      (err: any) => console.log(err),
      () => console.log('getUserActivity() retrieved user activities'));

  }

  isAdmin() {
    return this.authenticationService.isAdminUser();
  }

  selectUser(user: User) {

    this.currentUser = user;

    this.getUserActivities(user.id, this.recordsCount, this.fromCreateDate, this.toCreateDate, this.activityType);
  }
  filterByActivityType(activityType) {

    this.activityType = activityType;

    this.getUserActivities(this.currentUser.id, this.recordsCount, this.fromCreateDate, this.toCreateDate, activityType);
  }
  selectRecords(records: number) {

    this.recordsCount = records;

    this.getUserActivities(this.currentUser.id, this.recordsCount, this.fromCreateDate, this.toCreateDate, this.activityType);
  }

  filterByDate() {

    this.getUserActivities(this.currentUser.id, this.recordsCount, this.fromCreateDate, this.toCreateDate, this.activityType);
  }

  getUsers() {
    
    this.dataService.getUsers("")
      .subscribe((response: User[]) => {
        this.usersList = response;

      },
      (err: any) => console.log(err),
      () => console.log('getUsers() retrieved portal users'));

  }

  takeAction(activity: UserActivity) {

    if (activity.activityType == "customer") {

      this.dataService.getCustomer(activity.activityId)
        .subscribe((response: Customer) => {

          var customer: Customer = response;
          this.dataService.setSelectedCustomer(customer);

          if (customer.type == "Business") {
            this.router.navigateByUrl("/customer/business-customer");
          }
          else {
            this.router.navigateByUrl("/customer/customer");
          }
        },
        (err: any) => console.log(err),
        () => console.log('getCustomer retrieved customer'));
    }
    else if (activity.activityType == "portal user") {

      this.dataService.selectedUserId$ = activity.activityId;
      this.router.navigateByUrl("/users/user");
    }
  }

  getSubscribersCount() {

    this.dataService.getCustomersStatistics(this.authenticationService.loggedUser$.id)
      .subscribe((response: CustomerStatistics[]) => {
        console.log(response);
        this.customerStatisticsList = response;


        var data = [];
        var totalPoints = 2500;
        function showChartTooltip(x, y, xValue, yValue) {
          $('<div id="tooltip" class="chart-tooltip">' + yValue + '<\/div>').css({
            position: 'absolute',
            display: 'none',
            top: y - 40,
            left: x - 40,
            border: '0px solid #ccc',
            padding: '2px 6px',
            'background-color': '#fff'
          }).appendTo("body").fadeIn(200);
        }

        var statsRegCustomers = [];
        var statsBonus = [];
        for (let stat of this.customerStatisticsList) {
          statsBonus.splice(0, 0, [stat.mounth, stat.count * 10])
          statsRegCustomers.splice(0, 0, [stat.mounth, stat.count])
        }
      
        if (this.customerStatisticsList.length != 0) {

          $('#site_reg_customers_loading').hide();
          $('#site_reg_customers_content').show();

          var plot_statistics = $.plot($("#site_reg_customers"), [{
            data: statsRegCustomers,
            lines: {
              fill: 0.6,
              lineWidth: 0
            },
            color: ['#f89f9f']
          }, {
              data: statsRegCustomers,
            points: {
              show: true,
              fill: true,
              radius: 5,
              fillColor: "#f89f9f",
              lineWidth: 3
            },
            color: '#fff',
            shadowSize: 0
          }],

            {
              xaxis: {
                tickLength: 0,
                tickDecimals: 0,
                mode: "categories",
                min: 0,
                font: {
                  lineHeight: 14,
                  style: "normal",
                  variant: "small-caps",
                  color: "#6F7B8A"
                }
              },
              yaxis: {
                ticks: 5,
                tickDecimals: 0,
                tickColor: "#eee",
                font: {
                  lineHeight: 14,
                  style: "normal",
                  variant: "small-caps",
                  color: "#6F7B8A"
                }
              },
              grid: {
                hoverable: true,
                clickable: true,
                tickColor: "#eee",
                borderColor: "#eee",
                borderWidth: 1
              }
            });

          var previousPoint = null;
          $("#site_reg_customers").bind("plothover", function (event, pos, item) {
            $("#x").text(pos.x.toFixed(2));
            $("#y").text(pos.y.toFixed(2));
            if (item) {
              if (previousPoint != item.dataIndex) {
                previousPoint = item.dataIndex;

                $("#tooltip").remove();
                var x = item.datapoint[0].toFixed(2),
                  y = item.datapoint[1].toFixed(2);

                showChartTooltip(item.pageX, item.pageY, item.datapoint[0], item.datapoint[1] + ' customers');
              }
            } else {
              $("#tooltip").remove();
              previousPoint = null;
            }
          });
        }

        if (this.customerStatisticsList.length != 0) {
          //site activities
          var previousPoint2 = null;
          $('#site_bonus_loading').hide();
          $('#site_bonus_content').show();

        
          var plot_statistics = $.plot($("#site_bonus"),

            [{
              data: statsBonus,
              lines: {
                fill: 0.2,
                lineWidth: 0,
              },
              color: ['#BAD9F5']
            }, {
                data: statsBonus,
              points: {
                show: true,
                fill: true,
                radius: 4,
                fillColor: "#9ACAE6",
                lineWidth: 2
              },
              color: '#9ACAE6',
              shadowSize: 1
            }, {
                data: statsBonus,
              lines: {
                show: true,
                fill: false,
                lineWidth: 3
              },
              color: '#9ACAE6',
              shadowSize: 0
            }],

            {

              xaxis: {
                tickLength: 0,
                tickDecimals: 0,
                mode: "categories",
                min: 0,
                font: {
                  lineHeight: 18,
                  style: "normal",
                  variant: "small-caps",
                  color: "#6F7B8A"
                }
              },
              yaxis: {
                ticks: 5,
                tickDecimals: 0,
                tickColor: "#eee",
                font: {
                  lineHeight: 14,
                  style: "normal",
                  variant: "small-caps",
                  color: "#6F7B8A"
                }
              },
              grid: {
                hoverable: true,
                clickable: true,
                tickColor: "#eee",
                borderColor: "#eee",
                borderWidth: 1
              }
            });

          $("#site_bonus").bind("plothover", function (event, pos, item) {
            $("#x").text(pos.x.toFixed(2));
            $("#y").text(pos.y.toFixed(2));
            if (item) {
              if (previousPoint2 != item.dataIndex) {
                previousPoint2 = item.dataIndex;
                $("#tooltip").remove();
                var x = item.datapoint[0].toFixed(2),
                  y = item.datapoint[1].toFixed(2);
                showChartTooltip(item.pageX, item.pageY, item.datapoint[0], item.datapoint[1] + 'M$');
              }
            }
          });

          $('#site_bonus').bind("mouseleave", function () {
            $("#tooltip").remove();
          });
        }

      },
      (err: any) => console.log(err),
      () => console.log('getCustomersCountByMonth() retrieved count'));
  }

  ngOnInit(): void {
    // Handles counterup plugin wrapper
    this.getSubscribersCount();
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/home/welcome";

    this.currentUser = this.authenticationService.loggedUser$;
    this.getUserActivities(this.authenticationService.loggedUser$.id, this.recordsCount, this.fromCreateDate, this.toCreateDate, this.activityType);
    this.dataService.menuUnselect();

    if (this.authenticationService.isAdminUser()) {
      this.getUsers();

    }

    $("#menuWelcome").addClass("active");

    $("[data-counter='counterup']").counterUp({
      delay: 10,
      time: 1000
    });

    function showChartTooltip(x, y, xValue, yValue) {
      $('<div id="tooltip" class="chart-tooltip">' + yValue + '<\/div>').css({
        position: 'absolute',
        display: 'none',
        top: y - 40,
        left: x - 40,
        border: '0px solid #ccc',
        padding: '2px 6px',
        'background-color': '#fff'
      }).appendTo("body").fadeIn(200);
    }

    var data = [];
    var totalPoints = 2500;

    // random data generator for plot charts

    function getRandomData() {
      if (data.length > 0) data = data.slice(1);
      // do a random walk
      while (data.length < totalPoints) {
        var prev = data.length > 0 ? data[data.length - 1] : 50;
        var y = prev + Math.random() * 10 - 5;
        if (y < 0) y = 0;
        if (y > 100) y = 100;
        data.push(y);
      }
      // zip the generated y values with the x values
      var res = [];
      for (var i = 0; i < data.length; ++i) res.push([i, data[i]])
      return res;
    }

    function randValue() {
      return (Math.floor(Math.random() * (1 + 50 - 20))) + 10;
    }

   

  

    



  }
}
