import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef, ViewChild, AfterViewInit, ElementRef } from '@angular/core';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { MenuItem } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { ScriptService } from '../script.service';
import { Customer } from '../customers/customer';
import { Contract } from './contract';
import { LegalContract } from './legal.contract';
import { DataService } from '../data.service';
import { SearchCriteria } from '../shared/search.criteria';
import { PagerService } from '../shared/pager.service';
import { ICustomerResults } from '../shared/interfaces';
import { environment } from '../../environments/environment';
import { AuthenticationService } from '../services/index';
declare var $: any;
declare var SignaturePad: any;
//declare var theCanvas: any;

@Component({

  templateUrl: '/print.contract.component.html'
})
export class PrintContractComponent implements AfterViewInit {
  pageTitle: string = 'Print Contract';
  //context: CanvasRenderingContext2D;
  //canvas: HTMLCanvasElement;
  //@ViewChild("thecanvas") canvasObject: ElementRef;
  //@ViewChild("thecanvas") theCanvas;

  signaturePad: any;
  canvas: HTMLCanvasElement;

  public contract: Contract;
  public acceptTerms: boolean;
  public legalContract: LegalContract = new LegalContract();
  
  
  constructor(private dataService: DataService,
    private router: Router,
    private authenticationService: AuthenticationService,
    public toastr: ToastrService,
    vcr: ViewContainerRef,
    private pagerService: PagerService) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  saveSignature(): void {

    alert(this.signaturePad.toDataURL("image/png"));
    //window.open(signaturePad.toDataURL("image/png"));
  }

  clearSignature(): void {
    
    this.signaturePad.clear();
    this.drawSignatureLine();
  }

  drawSignatureLine(): void {

    //// var signaturePad = new SignaturePad((<HTMLCanvasElement>this.canvasObject.nativeElement));
    //let canvas = this.theCanvas.nativeElement;
    ////var context = (<HTMLCanvasElement>this.canvasObject.nativeElement).getContext('2d');
    //this.context = canvas.getContext("2d");
    //this.context.lineWidth = .5;
    //this.context.strokeStyle = '#5534';
    //this.context.beginPath();
    //this.context.moveTo(0, 150);
    //this.context.lineTo(500, 150);
    //this.context.stroke();
    
    var ctx: CanvasRenderingContext2D;
          
    ctx = this.canvas.getContext('2d');
                
    ctx.lineWidth = .5;
    ctx.strokeStyle = '#5534';
    ctx.beginPath();
    ctx.moveTo(0, 160);
    ctx.lineTo(500, 160);
    ctx.stroke();
   
  }

  saveLegalContract(form): void {

    //console.log(this.signaturePad.toDataURL("image/png"));

    this.legalContract.customerId = this.contract.customerid;
    this.legalContract.contractId = this.contract.id;
    this.legalContract.createdByUserId = this.authenticationService.loggedUser$.id;
    this.legalContract.signature = this.signaturePad.toDataURL("image/png");

      this.dataService.saveLegalContract(this.legalContract)
        .subscribe((legalContract: LegalContract) => {
          
            //this.contract.id = contract.id;
          this.toastr.success('Creation Successfully Completed!', 'Success!');
          window.print();
            //this.dataService.setSelectedContract(new Contract());
          },
      (err: any) => this.toastr.error(err.json().message));
     
  }


  printContract(): void {

    $("#clearButton").hide();
    window.print();
  }
  ngOnInit(): void {
    this.acceptTerms = false;
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/contract/print-contract";

    this.contract = this.dataService.printedContract$;

    //this.drawSignatureLine();
  }



  ngAfterViewInit(): void {

    var container: any;
    container = document.getElementById('canvasContainer');
    this.canvas = document.createElement('canvas');
    this.canvas.width = 475;
    this.canvas.height = 180;
    container.appendChild(this.canvas);

    this.signaturePad = new SignaturePad(this.canvas);

    this.drawSignatureLine();

  }
}

