import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/index';
import { ScriptService } from '../../script.service';
import { ZonePackage } from './zonepackage';
import { DataService } from '../../data.service';

declare var $: any;

@Component({
    templateUrl: '/zonepackage.search.component.html',
})
export class ZonePackageSearchComponent implements OnInit {
    pageTitle: string = 'Search Zone Package';
    userSearch: string = "";
    currentProgress: number = 0;
    scriptService: ScriptService = new ScriptService();
    public searchZonePackage: string;
    isLoading: boolean = false;

    public zonePackagesList: ZonePackage[] = [];

    constructor(private dataService: DataService,
        public toastr: ToastrService,
        private authenticationService: AuthenticationService,
        private router: Router,
        vcr: ViewContainerRef) {
    }

    getZonePackages() {

        this.isLoading = true;

        this.dataService.getZonePackages()
            .subscribe((response: ZonePackage[]) => {
                this.zonePackagesList = response;
                this.dataService.cacheZonePackageSearch(this.zonePackagesList);
                this.isLoading = false;
            },
                (err: any) => console.log(err),
                () => console.log('getZonePackages() retrieved zone packages'));


        console.log(this.zonePackagesList);
    }


    ngOnInit(): void {
        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/zonepackage-search";
        this.getZonePackages();

        this.dataService.menuUnselect();
        $("#menuZoneTariffs").parent().parent().addClass("open");
        $("#menuZoneTariffs").parent().show();
        $("#menuZoneTariffs").addClass("active");
    }

    editZonePackage(zonePackage: ZonePackage) {
        this.dataService.setSelectedZonePackage(zonePackage);
    }

    addZonePackage() {

        var newZonePackage = new ZonePackage();
        newZonePackage.id = 0;

        this.dataService.setSelectedZonePackage(newZonePackage);
    }

    delete(zonePackageId) {
        event.preventDefault();
        this.dataService.deleteZonePackage(zonePackageId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The zone package was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let zonePackage of this.zonePackagesList) {

                        if (zonePackage.id == zonePackageId)
                            this.zonePackagesList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete zone package!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.message));    
        }

    
}
