import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { SiteLayoutComponent } from '../layout/site-layout.component';
import { CustomerComponent } from './customer.component';
import { CustomerFilterPipe } from './customer-filter.pipe';
import { BusinessCustomerComponent } from './business.customer.component';
import { CustomerSearchComponent } from './customer.search.component';
import { PrintContractComponent } from './print.contract.component';
import { AuthGuard } from '../guards/auth.guard';
import { CustomerService } from './customer.service';
import { SharedModule } from '../shared/shared.module';

@NgModule({
  imports: [

    BrowserModule,
    HttpModule,
    CommonModule,
    FormsModule,
    SharedModule,
    RouterModule.forRoot([

      {
        path: 'customer',
        component: SiteLayoutComponent,
        children: [
          { path: 'customer', component: CustomerComponent, canActivate: [AuthGuard] },
          { path: 'customer-search', component: CustomerSearchComponent, canActivate: [AuthGuard] },
          { path: 'business-customer', component: BusinessCustomerComponent, canActivate: [AuthGuard] },
          { path: 'business-customer/:id', component: BusinessCustomerComponent, canActivate: [AuthGuard] },
          { path: 'customer/:id', component: CustomerComponent, canActivate: [AuthGuard] },
          { path: 'print-contract', component: PrintContractComponent, canActivate: [AuthGuard] }
        ]
      }
    ])
  ],
  declarations: [
    CustomerComponent,
    CustomerSearchComponent,
    BusinessCustomerComponent,
    PrintContractComponent,
    CustomerFilterPipe

  ],
  providers: [
    CustomerService,

  ]
})
export class CustomersModule { }

