import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { User } from '../users/user';
import { Router } from '@angular/router';
import { Customer } from '../customers/customer';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import 'rxjs/add/operator/map'

@Injectable()
export class AuthenticationService {
  public token: string;

    public serverUrl: string = "https://178.63.51.3:55368";
  //public serverUrl: string = environment.url;
  //public serverUrl: string = "http://localhost:5555";

  public loggedUser$: User;

  constructor(private http: Http, private router: Router) {
    // set token if saved in local storage
    // var currentUser = JSON.parse(localStorage.getItem('currentUser'));
    // this.token = currentUser && currentUser.token;
  }

  saveCustomer(customer: Customer): Observable<Customer> {
   
    var url: string = this.serverUrl + '/api/customers/save';
   
    console.log(customer);

    return this.http.post(url, customer)
      .map((res: Response) => {

        console.log(res);
        return customer;
      })
  }

  login(username: string, password: string): Observable<boolean> {

    var url: string = this.serverUrl + '/api/portalusers/login/' + username + "/" + password;

    //return this.http.post(this.serverUrl + '/api/portalusers/login/', JSON.stringify({ username: username, password: password }))
    return this.http.get(url)
      .map((response: Response) => {

        console.log(response);

        this.loggedUser$ = response.json();
        this.loggedUser$.isAdmin = false;

        for (let userGroup of this.loggedUser$.userGroups) {
          if (userGroup.id == 1)
            this.loggedUser$.isAdmin = true;
        }

        if (this.loggedUser$.id > 0) {
          return true;
        }
        else {
          return false
        }


      });
  }

  logout(): void {

    this.loggedUser$ = null;
    this.router.navigate(['/login']);
  }

  isLoggedUser() {
    if (this.loggedUser$ != null && this.loggedUser$.id > 0)
      return true;

    return false;
  }

  isAdminUser() {

    if (this.loggedUser$ != null && this.loggedUser$.id > 0 && this.loggedUser$.isAdmin)
      return true;

    return false;
  }


}

