/* Defines the product entity */
import { Address } from './address';

export class Customer {
  id: number;
  parentId: number;
  customerNo: string;
  hasChildren: boolean;
  isForInvoice: boolean;
  isSelected: boolean;
  isForContract: boolean;
  type: string;
  status: string;
  oldStatus: string;
  firstName: string;
  searchText: string;
  isStillLargeAccount: boolean;
  lastName: string;
  title: string;
  email: string;
  age: string;
  gender: string;
  addresses: Array<Address> = [];
  phone: string;
  contacts: string;
  fax: string;
  companyName: string;
  createdByUserId: number;
  createdDate: Date;
  isForDelete: boolean;
  totalRows: number;

  constructor() {
    this.id = 0;
  }
}

