import { Component, OnInit} from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { TariffModel } from './tariffmodel';
import { TariffNode } from './tariffnode';
import { TimePackage } from '../timepackage/timepackage';
import { ZonePackage } from '../zonepackage/zonepackage';
import { UnitOfMeasure } from './unitofmeasure';

declare var $: any;

@Component({
    templateUrl: 'tariffmodel.component.html'
})
export class TariffModelComponent implements OnInit {

    pageTitle: string = 'Tariff Model';

    public tariffNodesList: TariffNode[] = [];
    public timePackagesList: TimePackage[] = [];
    public zonePackagesList: ZonePackage[] = [];
    public unitsOfMesureList: UnitOfMeasure[] = [];
    //zoneentry: ZoneEntry = new ZoneEntry();
    isTariffNodesLoading: boolean = false;
    tariffmodel: TariffModel = new TariffModel();
    tariffnode: TariffNode = new TariffNode();
    selectedUnitOfMeasureId: number;
    selectedTimePackageId: number;
    selectedZonePackageId: number;
    currentProgress: number = 0;
    isForSaveGenerateNodes: boolean = false;


    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
    }

    cancel(): void {

        this.dataService.setSelectedTariffModel(new TariffModel());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveTariffModel(): void {

        this.isForSaveGenerateNodes = false;

        var isValid = true;        
        this.tariffmodel.createdByUserId = this.authenticationService.loggedUser$.id;
       

        if (this.tariffmodel.id == 0 || this.tariffmodel.id == undefined || this.tariffmodel.id == null) {

            this.tariffmodel.id = 0;
        
            //isValid = false
        }

        if (isValid) {     
            
            var oldTimePackage = this.tariffmodel.timePackageId;
            var oldZonePackage = this.tariffmodel.zonePackageId;

            this.tariffmodel.unitOfMeasureId = this.selectedUnitOfMeasureId;
            this.tariffmodel.timePackageId = this.selectedTimePackageId;
            this.tariffmodel.zonePackageId = this.selectedZonePackageId;

            console.log(this.tariffmodel);
            this.dataService.saveTariffModel(this.tariffmodel)
                .subscribe((tariffmodel: TariffModel) => {

                    this.tariffmodel.id = tariffmodel.id;

                    if (oldTimePackage != this.selectedTimePackageId || oldZonePackage != this.selectedZonePackageId) {                        
                        this.isForSaveGenerateNodes = true;                       
                    }
                    
                    this.tariffmodel = tariffmodel;
                    this.toastr.success('The tariff model was successfully saved!', 'Success!');
                   
                    this.dataService.setSelectedTariffModel(this.tariffmodel);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
        
    }

    generateTariffNodes(): void {
                
      console.log(this.tariffmodel);
      this.dataService.generateTariffNodes(this.tariffmodel)
          .subscribe((status: boolean) => {
              if (status) {

                  this.toastr.success('The tariff nodes were generated successfully!', 'Success!');


                  this.loadTariffNodes(this.tariffmodel.id);
              }
          },
              (err: any) => this.toastr.error(err.json().message));
        

    }

    getUnitsOfMeasure(): void {
        this.dataService.getUnitsOfMeasure()
            .subscribe((response: UnitOfMeasure[]) => {

                this.unitsOfMesureList = response;
                console.log(this.unitsOfMesureList);
                
                if (this.dataService.getSelectedTariffModel().unitOfMeasureId > 0) {

                    this.selectedUnitOfMeasureId = this.dataService.getSelectedTariffModel().unitOfMeasureId;
                }
                else {

                    this.selectedUnitOfMeasureId = this.unitsOfMesureList[0].id;
                }
            },
                (err: any) => console.log(err),
                () => console.log('getUnitsOfMeasure retrieved units of measure'));
        
    }

    getTimePackages(): void {
        this.dataService.getTimePackages()
            .subscribe((response: TimePackage[]) => {

                this.timePackagesList = response;
                console.log(this.timePackagesList);
                
                if (this.dataService.getSelectedTariffModel().timePackageId > 0) {

                    this.selectedTimePackageId = this.dataService.getSelectedTariffModel().timePackageId;
                }
                else {

                    this.selectedTimePackageId = this.timePackagesList[0].id;
                }


            },
                (err: any) => console.log(err),
                () => console.log('getTimePackages retrieved time packages'));
        
    }

    getZonePackages(): void {
        this.dataService.getZonePackages()
            .subscribe((response: ZonePackage[]) => {

                this.zonePackagesList = response;
                console.log(this.zonePackagesList);
                
                if (this.dataService.getSelectedTariffModel().zonePackageId > 0) {

                    this.selectedZonePackageId = this.dataService.getSelectedTariffModel().zonePackageId;
                }
                else {

                    this.selectedZonePackageId = this.zonePackagesList[0].id;
                }

            },
                (err: any) => console.log(err),
                () => console.log('getZonePackages retrieved zone packages'));

    }

    ngOnInit(): void {

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/tariffmodel";
        
        this.tariffmodel = this.dataService.getSelectedTariffModel();
        
        console.log(this.tariffmodel);

        if (this.dataService.getSelectedTariffModel().id > 0) {

            this.isTariffNodesLoading = true;
            this.loadTariffNodes(this.dataService.getSelectedTariffModel().id);
            this.selectedUnitOfMeasureId = this.dataService.getSelectedTariffModel().unitOfMeasureId;
            this.selectedTimePackageId = this.dataService.getSelectedTariffModel().timePackageId;
            this.selectedZonePackageId = this.dataService.getSelectedTariffModel().zonePackageId;
        }
        else {
            this.clearTariffModel(null);
        }

        this.getUnitsOfMeasure();
        this.getTimePackages();
        this.getZonePackages();
        this.dataService.menuUnselect();
    }

    loadTariffNodes(id) {
        this.dataService.getTariffNodes(this.dataService.getSelectedTariffModel().id)
            .subscribe((response: TariffNode[]) => {
                this.isTariffNodesLoading = false;
                this.tariffNodesList = response;
                console.log(this.tariffNodesList);

            },
                (err: any) => console.log(err),
                () => console.log('getTariffNodes retrieved tariff nodes'));
    }

    clearTariffModel(form) {
        this.tariffmodel = new TariffModel();
        this.tariffmodel.id = 0;
        //this.zone.zonepackageId = this.dataService.getSelectedZonePackage().id;
        //this.zoneentry.zoneId = this.dataService.getSelectedZone().id;
        this.dataService.setSelectedTariffModel(new TariffModel());

        if (form != null)
            form.reset();
    }

    calcTariffModelProgress(): number {

        var current: number = 75;

        if (!this.checkIsEmpty(this.tariffmodel.name))
            current += 25;
        
        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editTariffNode(tariffnode: TariffNode) {
        console.log(tariffnode);

        this.dataService.setSelectedTariffNode(tariffnode);
        this.tariffnode = tariffnode;

        //this.selectedZoneDestinationId = this.zoneentry.zoneDestinationId;
        //this.selectedZoneOriginId = this.zoneentry.zoneOriginId;
    }
    addTariffNode() {
        this.tariffnode = new TariffNode();
        this.tariffnode.id = 0;
        this.tariffnode.tariffModelId = this.dataService.getSelectedTariffModel().id;

        this.dataService.setSelectedTariffNode(this.tariffnode);
    }
    delete(tariffNodeId) {
        event.preventDefault();
        this.dataService.deleteTariffNode(tariffNodeId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The tariff node was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let tariffnode of this.tariffNodesList) {

                        if (tariffnode.id == tariffNodeId)
                            this.tariffNodesList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete tariff node!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.message));
    }
}

