import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/index';
import { ScriptService } from '../../script.service';
import { DataService } from '../../data.service';
import { TimePackage } from './timepackage';

declare var $: any;

@Component({
  templateUrl: '/timepackage.search.component.html',
})
export class TimePackageSearchComponent implements OnInit {
  pageTitle: string = 'Search Time Package';
    userSearch: string = "";
    public searchTimePackage: string;
  currentProgress: number = 0;
  scriptService: ScriptService = new ScriptService();

  isLoading: boolean = false;

  public timePackagesList: TimePackage[] = [];

  constructor(private dataService: DataService,
    public toastr: ToastrService,
    private authenticationService: AuthenticationService,
    private router: Router,
    vcr: ViewContainerRef) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  getTimePackages() {

    this.isLoading = true;

    this.dataService.getTimePackages()
      .subscribe((response: TimePackage[]) => {
        this.timePackagesList = response;
        this.dataService.cacheTimePackageSearch(this.timePackagesList);
        this.isLoading = false;
      },
        (err: any) => console.log(err),
        () => console.log('getTimePackages() retrieved time packages'));

    
    console.log(this.timePackagesList);
  }


  ngOnInit(): void {
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/tariffs/timepackage-search";   
    this.getTimePackages();

    this.dataService.menuUnselect();
    $("#menuTimeTariffs").parent().parent().addClass("open");
    $("#menuTimeTariffs").parent().show();
    $("#menuTimeTariffs").addClass("active");
  }

  editTimePackage(timePackage: TimePackage) {
    this.dataService.setSelectedTimePackage(timePackage);
  }

  addTimePackage() {

    var newTimePackage = new TimePackage();
    newTimePackage.id = 0;

    this.dataService.setSelectedTimePackage(newTimePackage);
  }

  delete(timePackageId) {
      event.preventDefault();
      this.dataService.deleteTimePackage(timePackageId)
        .subscribe((status: boolean) => {
          if (status) {
            this.toastr.success('The time package was successfully deleted!', 'Success!');
            // this.router.navigate(['/customer/customer-search']);

            var currentIndex: number = 0;
            for (let timePackage of this.timePackagesList) {

                if (timePackage.id == timePackageId)
                  this.timePackagesList.splice(currentIndex, 1);

              currentIndex++;
            }

          }
          else {
            this.toastr.error('Unable to delete time package!', 'Error!');
          }
        },
        (err: any) => this.toastr.error(err.json().message));
    }

}
