import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { MenuItem } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { ScriptService } from '../script.service';
import { Invoice } from './invoice';
import { Customer } from '../customers/customer';
import { DataService } from '../data.service';
import { SearchCriteria } from '../shared/search.criteria';
import { PagerService } from '../shared/pager.service';
import { ICustomerResults } from '../shared/interfaces';
import { environment } from '../../environments/environment';
declare var $: any;

@Component({

  templateUrl: '/invoice.component.html'
})
export class InvoiceComponent {
  pageTitle: string = 'Generate Invoice';
  value: Date;
  invoice: Invoice = new Invoice();
  selectedCustomerFromTemplate: Customer;
  public invoicesList: Invoice[] = [];
  
  public customersList: Customer[] = [];
  public onDemand: boolean = false;
  public showDetail: boolean = false;

  currentProgress: number = 0;
  scriptService: ScriptService = new ScriptService();
  public searchCriteria: SearchCriteria = new SearchCriteria();
  public simpleSearchCriteria: SearchCriteria = new SearchCriteria();
  deleteMessageEnabled: boolean;
  //public customersList: Customer[] = [];
  totalRecords: number = 0;
  page: number = 1;
  isFirstLoad: boolean = true;
  isLoading: boolean = false;
  customerSearch: string = "";
  invoiceReady: boolean = false;

  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];

  constructor(private dataService: DataService,
    private router: Router,
    public toastr: ToastrService,
    vcr: ViewContainerRef,
    private pagerService: PagerService) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  updateFromTemplate($event) {
    this.selectedCustomerFromTemplate = $event;
    //console.log("this.selectedCustomerFromTemplate :" + this.selectedCustomerFromTemplate.firstName);
    if (this.selectedCustomerFromTemplate  != null) {
      this.invoiceReady = true;
    }
    else {
      this.invoiceReady = false;
    }
  }

  changeInvoiceType(invoiceType: boolean): void {
    this.onDemand = invoiceType;
  }

  generateInvoice(): void {
    this.toastr.success('Invoice Creation Successfully Completed!', 'Success!');
    this.showDetail = true;
    
    this.router.navigate(['/', 'billing', 'print-invoice']);
  }

  getInvoices() {

    //this.invoicesList = this.dataService.getInvoices();
   
  }

  printInvoice() {
    //this.printcontent($("#head").html() + '<br/>' + $("#content").html());
   
    var mywindow = window.open('', '', '');
    mywindow.document.write('<html><body>');
    mywindow.document.write($("#section-to-print").html());
    mywindow.document.write('</body></html>');
    mywindow.document.close();
    mywindow.print();
    return true;
  }

  forInvoice(customer: Customer) {
   
    if (!customer.isForInvoice) {

      this.invoiceReady = true;      

      for (let cust of this.customersList) {

        if (cust.id != customer.id) {          
          cust.isForInvoice = false;
        }

      }

    }
    else {
      this.invoiceReady = false;
    }

  }


  saveInvoice(): void {

    this.dataService.addInvoice(this.invoice);
    this.clearInvoice();
  }

  ngOnInit(): void {
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/billing/invoice";

    this.dataService.menuUnselect();
    $("#menuGenerateInvoice").parent().parent().addClass("open");
    $("#menuGenerateInvoice").parent().show();
    $("#menuGenerateInvoice").addClass("active");

   // this.getCustomers();
    this.getInvoices();

     
    this.customersList = []; 

  }

  clearInvoice() {
    this.invoice = new Invoice();
  }


}

