import { Component, OnInit } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router} from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { TimePackage } from './timepackage';
import { TimeBand } from './timeband';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services';
declare var $: any;

@Component({
    templateUrl: 'timepackage.component.html'
})
export class TimePackageComponent implements OnInit {

    pageTitle: string = 'Time Package Detail';
    public showAddress: boolean = false;
    public timeBandsList: TimeBand[] = [];
    isTimeBandsLoading: boolean = false;
    timepackage: TimePackage = new TimePackage();

    currentProgress: number = 0;

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
        //this.toastr.setRootViewContainerRef(vcr);
    }


    cancel(): void {

        this.dataService.setSelectedTimePackage(new TimePackage());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }


    saveTimePackage(form): void {
        var isValid = true;

        this.timepackage.createdByUserId = this.authenticationService.loggedUser$.id;

        if (this.timepackage.id == 0 || this.timepackage.id == undefined || this.timepackage.id == null) {

            this.timepackage.id = 0;
        }
        else {
            isValid = false
        }

        if (isValid) {
            this.dataService.saveTimePackage(this.timepackage)
                .subscribe((timepackage: TimePackage) => {

                    this.timepackage = timepackage;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');
                    this.dataService.setSelectedTimePackage(this.timepackage);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }

    }

    ngOnInit(): void {

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/timepackage";
        this.timepackage = this.dataService.getSelectedTimePackage();
        console.log(this.timepackage);
        if (this.dataService.getSelectedTimePackage().id > 0) {

            this.isTimeBandsLoading = true;
            this.dataService.getTimeBands(this.dataService.getSelectedTimePackage().id)
                .subscribe((response: TimeBand[]) => {
                    this.isTimeBandsLoading = false;

                    this.timeBandsList = response;
                    this.dataService.cacheTimeBandSearch(this.timeBandsList);
                    console.log(this.timeBandsList);

                },
                    (err: any) => console.log(err),
                    () => console.log('getTimeBands retrieved time bands'));

        }
        else {
            this.clearTimePackage(null);
        }
        this.dataService.menuUnselect();
    }

    clearTimePackage(form) {
        this.timepackage = new TimePackage();
        this.timepackage.id = 0;
        //this.customer.status = "Active";
        this.dataService.setSelectedTimePackage(new TimePackage());

        if (form != null)
            form.reset();
    }

    calcTimePackageProgress(): number {

        var current: number = 0;

        if (!this.checkIsEmpty(this.timepackage.name))
            current += 100;


        if (current > 100)
            current = 100;

        this.currentProgress = current;

        if (current > 10)
            $("#businessCustomerEditMode").show();
        else
            $("#businessCustomerEditMode").hide();

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editTimeBand(timeBand: TimeBand) {

        this.dataService.setSelectedTimeBand(timeBand);

    }
    addTimeBand() {

        var newTimeBand = new TimeBand();
        newTimeBand.id = 0;
        newTimeBand.timepackageId = this.dataService.getSelectedTimePackage().id;

        this.dataService.setSelectedTimeBand(newTimeBand);
    }

    delete(timeBandId) {
        event.preventDefault();
        this.dataService.deleteTimeBand(timeBandId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The time band was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let timeBand of this.timeBandsList) {

                        if (timeBand.id == timeBandId)
                            this.timeBandsList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete time band!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.json().message));
    }

}

