import { Component, OnInit} from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { TariffPrice } from './tariffprice';
import { TariffNode } from './tariffnode';
import { TimeBand } from '../timepackage/timeband';
import { Zone } from '../zonepackage/zone';

declare var $: any;

@Component({
    templateUrl: 'tariffnode.component.html'
})
export class TariffNodeComponent implements OnInit {

    pageTitle: string = 'Tariff Node Detail';

    public tariffPricesList: TariffPrice[] = [];
    public timeBandsList: TimeBand[] = [];
    public zonesList: Zone[] = [];
    tariffprice: TariffPrice = new TariffPrice();
    isTariffPricesLoading: boolean = false;
    tariffnode: TariffNode = new TariffNode();
    selectedTimeBandId: number;
    selectedZoneId: number;

    currentProgress: number = 0;

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
        
    }

    cancel(): void {

        this.dataService.setSelectedTimeBand(new TimeBand());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveTariffNode(form): void {

        this.tariffnode.createdByUserId = this.authenticationService.loggedUser$.id;
        this.tariffnode.timeBandId = this.selectedTimeBandId;
        this.tariffnode.zoneId = this.selectedZoneId;

        if (this.tariffnode.id == undefined || this.tariffnode.id == null) {

            this.tariffnode.id = 0;
        }
              
            console.log(this.tariffnode);
            this.dataService.saveTariffNode(this.tariffnode)
                .subscribe((tariffnode: TariffNode) => {

                    this.tariffnode = tariffnode;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                    this.dataService.setSelectedTariffNode(this.tariffnode);
                },
                    (err: any) => this.toastr.error(err.json().message));
        
    }

    saveTariffPrice(form): void {

        var isValid = true;

        this.tariffprice.createdByUserId = this.authenticationService.loggedUser$.id;

        if (this.tariffnode.id == 0 || this.tariffnode.id == undefined || this.tariffnode.id == null) {

            this.tariffprice.tariffNodeId = 0;
            isValid = false
        }

        if (isValid) {
            console.log(this.tariffprice);
            this.dataService.saveTariffPrice(this.tariffprice)
                .subscribe((tariffprice: TariffPrice) => {
                    console.log(tariffprice);
                    this.tariffprice = tariffprice;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                    this.loadTariffPrices(this.dataService.getSelectedTariffNode().id);
                    $('#responsive').modal('toggle');
                    this.dataService.setSelectedTariffPrice(this.tariffprice);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
    }

    getTimeBands(): void {
        this.dataService.getTimeBands(this.dataService.getSelectedTariffModel().timePackageId)
            .subscribe((response: TimeBand[]) => {

                this.timeBandsList = response;
                console.log(this.timeBandsList);
                
                if (this.dataService.getSelectedTariffNode().timeBandId > 0) {

                    this.selectedTimeBandId = this.dataService.getSelectedTariffNode().timeBandId;
                }
                else {

                    this.selectedTimeBandId = this.timeBandsList[0].id;
                }

            },
                (err: any) => console.log(err),
                () => console.log('getTimeBands retrieved time bands'));
    }

    getZones(): void {
        this.dataService.getZones(this.dataService.getSelectedTariffModel().zonePackageId)
            .subscribe((response: Zone[]) => {

                this.zonesList = response;
                console.log(this.zonesList);
                
                if (this.dataService.getSelectedTariffNode().zoneId > 0) {

                    this.selectedZoneId = this.dataService.getSelectedTariffNode().zoneId;
                }
                else {

                    this.selectedZoneId = this.zonesList[0].id;
                }

            },
                (err: any) => console.log(err),
                () => console.log('getZones retrieved zones '));
    }

    ngOnInit(): void {
        
        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/tariffnode";
        this.tariffnode = this.dataService.getSelectedTariffNode();
        this.tariffprice = this.dataService.getSelectedTariffPrice();
        
        console.log(this.tariffnode);

        if (this.dataService.getSelectedTariffNode().id > 0) {

            this.isTariffPricesLoading = true;

            this.loadTariffPrices(this.dataService.getSelectedTariffNode().id);
            this.selectedTimeBandId = this.dataService.getSelectedTariffNode().timeBandId;
            this.selectedZoneId = this.dataService.getSelectedTariffNode().zoneId;
        }
        else {
            this.clearTariffNode(null);
        }
        this.getTimeBands();
        this.getZones();
        this.dataService.menuUnselect();

    }

    loadTariffPrices(id) {
       
        this.dataService.getTariffPrices(id)
            .subscribe((response: TariffPrice[]) => {
               
                this.isTariffPricesLoading = false;
                this.tariffPricesList = response;
                console.log(this.tariffPricesList);

            },
                (err: any) => console.log(err),
                () => console.log('getTariffPrices retrieved tariff prices'));
    }

    clearTariffNode(form) {
       
        this.tariffnode = new TariffNode();
        this.tariffnode.id = 0;
        this.tariffnode.tariffModelId = this.dataService.getSelectedTariffModel().id;
        this.tariffprice.tariffNodeId = this.dataService.getSelectedTariffNode().id;
        this.dataService.setSelectedTariffNode(new TariffNode());


        if (form != null)
            form.reset();
    }

    calcTariffNodeProgress(): number {

        var current: number = 100;

        //if (!this.checkIsEmpty(this.timeband.name))
        //    current += 100;

        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editTariffPrice(tariffprice: TariffPrice) {
        console.log(tariffprice);
        this.dataService.setSelectedTariffPrice(tariffprice);       
        this.tariffprice = { ...tariffprice }
    }

    addTariffPrice() {
        this.tariffprice = new TariffPrice();
        this.tariffprice.id = 0;
        this.tariffprice.tariffNodeId = this.dataService.getSelectedTariffNode().id;
        this.dataService.setSelectedTariffPrice(this.tariffprice);
    }

    delete(tariffPriceId) {
        event.preventDefault();
        this.dataService.deleteTariffPrice(tariffPriceId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The tariff price was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let tariffPrice of this.tariffPricesList) {

                        if (tariffPrice.id == tariffPriceId)
                            this.tariffPricesList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete tariff price!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.json().message));
    }
}

