import { Customer } from '../customers/customer';
import { Invoice } from '../billing/invoice';
import { Contract } from '../contracts/contract';
export class SearchCriteria {
  pageNumber: number;
  pageSize: number;
  userId: number;
  fromTotalAmount: number;
  activityType: string;
  toTotalAmount: number;
  fromTaxAmount: number;
  toTaxAmount: number;
  fromOpenAmount: number;
  toOpenAmount: number;
  fromInvoiceDate: Date;
  toInvoiceDate: Date;
  fromInvoiceDueDate: Date;
  toInvoiceDueDate: Date;

  customerCriteria: Customer;
  invoiceCriteria: Invoice;
  contractCriteria: Contract;
  constructor() {
    this.invoiceCriteria = new Invoice();
    this.customerCriteria = new Customer();
    this.contractCriteria = new Contract();
  }
}
