/* Defines the product entity */
import { RatePlan } from './rateplan';

export class Contract {
  id: number;  
  customerid: number;
  contractNo: string;
  type: string;
  typeStr: string;
  status: string;
  oldStatus: string;
  firstName: string;
  lastName: string;
  companyName: string;
  searchText: string;
  title: string;
  age: string;
  gender: string;
  contacts: string;
  msisdn: string;
  imsi: string;
  createdDate: Date;
  createdByUserId: number;
  ratePlan: RatePlan;
  isForDelete: boolean;
  totalRows: number;

  constructor() {
    this.id = 0;
    this.ratePlan = new RatePlan();
  }
}

