import { Component, OnInit} from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { ProductOffering } from './productoffering';
import { ProductOfferingService } from './productofferingservice';
import { BasicService } from './basicservice';
import { TariffModel } from '../pricing/tariffmodel';

declare var $: any;

@Component({
    templateUrl: 'productoffering.component.html'
})
export class ProductOfferingComponent implements OnInit {

    pageTitle: string = 'Product offering';

    public productOfferingServicesList: ProductOfferingService[] = [];
    public basicServicesList: BasicService[] = [];
    public tariffModelsList: TariffModel[] = [];
    isProductofferingServiceLoading: boolean = false;
    productoffering: ProductOffering = new ProductOffering();
    productofferingservice: ProductOfferingService = new ProductOfferingService();
    selectedBasicServiceId: number;
    selectedTariffModelId: number;
    currentProgress: number = 0;
    showTariffModel: boolean = false;
    

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
    }

    cancel(): void {

        this.dataService.setSelectedProductOffering(new ProductOffering());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveProductOffering(): void {       
        var isValid = true;        
        this.productoffering.createdByUserId = this.authenticationService.loggedUser$.id;
       
        if (this.productoffering.id == 0 || this.productoffering.id == undefined || this.productoffering.id == null) {

            this.productoffering.id = 0;
      
        }
        if (isValid) {     
            
            console.log(this.productoffering);
            this.dataService.saveProductOffering(this.productoffering)
                .subscribe((productoffering: ProductOffering) => {

                    this.productoffering.id = productoffering.id;
                    
                    this.productoffering = productoffering;
                    this.toastr.success('The product offering was successfully saved!', 'Success!');
                   
                    this.dataService.setSelectedProductOffering(this.productoffering);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
        
    }

    saveProductOfferingService(): void {

        var isValid = true;
        this.productofferingservice.createdByUserId = this.authenticationService.loggedUser$.id;


        if (this.productoffering.id == 0 || this.productoffering.id == undefined || this.productoffering.id == null) {

            this.productofferingservice.id = 0;
        }

        if (isValid) {

            this.productofferingservice.basicServiceId = this.selectedBasicServiceId;

            let selectedService = this.basicServicesList.find(i => i.id === this.selectedBasicServiceId);
            if (selectedService.usageInd) {
                this.productofferingservice.defaultTariffModelId = this.selectedTariffModelId;
            }
            else {
                this.productofferingservice.defaultTariffModelId = 0;
            }
                        
            console.log(this.productofferingservice);
            this.dataService.saveProductOfferingService(this.productofferingservice)
                .subscribe((productofferingservice: ProductOfferingService) => {

                    this.productofferingservice.id = productofferingservice.id;
                   
                    this.productofferingservice = productofferingservice;
                    this.toastr.success('The tariff model was successfully saved!', 'Success!');
                    this.loadProductOfferingServices();
                    this.dataService.setSelectedProductofferingService(this.productofferingservice);
                    $('#responsive').modal('toggle');
                },
                    (err: any) => this.toastr.error(err.json().message));
        }

    }
   
    getTariffModels(): void {
        this.dataService.getTariffModels()
            .subscribe((response: TariffModel[]) => {

                this.tariffModelsList = response;

                //var emptyTariffModel = new TariffModel();
                //emptyTariffModel.id = null;
                //emptyTariffModel.name = "";

                //this.tariffModelsList.unshift(emptyTariffModel);

                console.log(this.tariffModelsList);
                                
                this.selectedTariffModelId = this.tariffModelsList[0].id;
                
            },
                (err: any) => console.log(err),
                () => console.log('getTariffModels retrieved tariff models'));
        
    }

    getBasicServices(): void {
        this.dataService.getBasicServices()
            .subscribe((response: BasicService[]) => {

                this.basicServicesList = response;
                console.log(this.basicServicesList);
                               
                this.selectedBasicServiceId = this.basicServicesList[0].id;
                
            },
                (err: any) => console.log(err),
                () => console.log('getBasicServices retrieved basic services'));

    }

    ngOnInit(): void {

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/productoffering";
        
        this.productoffering = this.dataService.getSelectedProductOffering();
        
        console.log(this.productoffering);

        if (this.dataService.getSelectedProductOffering().id > 0) {

            this.isProductofferingServiceLoading = true;
            this.loadProductOfferingServices();
            
        }
        else {
            this.clearProductOffering(null);
        }

        //this.getBasicServices();
        //this.getTariffModels();

        this.getBasicServices();
        this.getTariffModels();

        this.dataService.menuUnselect();
    }

    loadProductOfferingServices() {
        this.dataService.getProductOfferingServices(this.dataService.getSelectedProductOffering().id)
            .subscribe((response: ProductOfferingService[]) => {
                this.isProductofferingServiceLoading = false;
                this.productOfferingServicesList = response;
                console.log(this.productOfferingServicesList);

            },
                (err: any) => console.log(err),
                () => console.log('getProductOfferingServices retrieved product offering services '));
    }

    clearProductOffering(form) {
        this.productoffering = new ProductOffering();
        this.productoffering.id = 0;
        
        this.dataService.setSelectedProductOffering(new ProductOffering());

        if (form != null)
            form.reset();
    }

    calcProductOfferingProgress(): number {

        var current: number = 75;

        if (!this.checkIsEmpty(this.productoffering.name))
            current += 25;
        
        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editProductOfferingService(productofferingservice: ProductOfferingService) {
        console.log(productofferingservice);

        this.dataService.setSelectedProductofferingService(productofferingservice);
        this.productofferingservice = productofferingservice;

        this.selectedBasicServiceId = productofferingservice.basicServiceId;
        this.selectedTariffModelId = productofferingservice.defaultTariffModelId; 

        console.log(this.basicServicesList);

        let selectedService = this.basicServicesList.find(i => i.id === this.selectedBasicServiceId);
        if (selectedService.usageInd)
            this.showTariffModel = true;
        else
            this.showTariffModel = false;

        this.productofferingservice = { ...productofferingservice }
    }
    addProductOfferingService() {
        this.productofferingservice = new ProductOfferingService();
        this.productofferingservice.id = 0;
        this.productofferingservice.productOfferingId = this.dataService.getSelectedProductOffering().id;

        this.dataService.setSelectedProductofferingService(this.productofferingservice);

        this.selectedBasicServiceId = this.basicServicesList[0].id
        this.selectedTariffModelId = this.tariffModelsList[0].id
        

    }

    delete(productOfferingServiceId) {
        event.preventDefault();
        this.dataService.deleteProductOfferingService(productOfferingServiceId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The product offering service was successfully deleted!', 'Success!');
                    
                    var currentIndex: number = 0;
                    for (let productofferingservice of this.productOfferingServicesList) {

                        if (productofferingservice.id == productOfferingServiceId)
                            this.productOfferingServicesList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete product offering service!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.message));
    }

    onBasicServiceSelected(event: any) {
       

        let selectedService = this.basicServicesList.find(i => i.id === this.selectedBasicServiceId);
        if (selectedService.usageInd)
            this.showTariffModel = true;
        else {
            this.showTariffModel = false;
            
        }
            
    }
}

