import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Customer } from '../../customers/customer';
import { Address } from '../../customers/address';
import { ToastrService } from 'ngx-toastr';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SearchCriteria } from '../search.criteria';
import { Subscription } from 'rxjs/Subscription';
import { ScriptService } from '../../script.service';

import { CustomerService } from '../../customers/customer.service';
import { DataService } from '../../data.service';
import { ICustomerResults } from '../interfaces';
import { PagerService } from '../pager.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'customersearch-template',
  templateUrl: './customer.search.template.html',

})

export class CustomerSearchTemplateComponent {

  @Input() hasSelection: boolean;
  @Output() valueChange = new EventEmitter();
  @Output()
  change: EventEmitter<Customer> = new EventEmitter<Customer>();
  @Output() customerChanged: EventEmitter<Customer> = new EventEmitter<Customer>();

  currentProgress: number = 0;
  scriptService: ScriptService = new ScriptService();
  public searchCriteria: SearchCriteria = new SearchCriteria();
  public simpleSearchCriteria: SearchCriteria = new SearchCriteria();
  deleteMessageEnabled: boolean;
  public customersList: Customer[] = [];
  selectedCustomer: Customer;
  totalRecords: number = 0;
  page: number = 1;
  isFirstLoad: boolean = true;
  isLoading: boolean = false;
  customerSearch: string = "";
  selectedReady: boolean = false;

  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];

  constructor(private dataService: DataService,
    private router: Router,
    public toastr: ToastrService,
    vcr: ViewContainerRef,
    private pagerService: PagerService) {
    //this.toastr.setRootViewContainerRef(vcr);
  }

  setPage(page: number, searchCriteria: SearchCriteria) {

    if (searchCriteria == null && this.simpleSearchCriteria.customerCriteria.firstName != null && this.simpleSearchCriteria.customerCriteria.firstName != "")
      searchCriteria = this.simpleSearchCriteria;
    else
      searchCriteria = this.searchCriteria;

    searchCriteria.pageSize = environment.pageSize;

    if (!this.isFirstLoad) {
      this.getCustomersPage(page, searchCriteria);
    }

    // get pager object from service

    this.pager = this.pagerService.getPager(this.totalRecords, page, searchCriteria.pageSize);
  }
  getCustomer(id) {
    //this.customersList = this.dataService.getCustomers();
    this.dataService.getCustomer(id)
      .subscribe((response: Customer) => {
        response.isSelected = true;

        //this.forInvoice(response);
        this.customersList = [];
        this.customersList.push(response);

      },
      (err: any) => console.log(err),
      () => console.log('getCustomerPage() retrieved customer by id'));
  }

  selectCustomer(customer: Customer) {
    

    if (!customer.isSelected) {

      this.selectedReady = true;

      for (let cust of this.customersList) {

        if (cust.id != customer.id) {
          cust.isSelected = false;
        }



      }
            
      this.customerChanged.emit(customer);
    }
    else {
     // alert("select");
      this.selectedReady = false;
      
      this.customerChanged.emit(null);
    }

  }

  getCustomers() {
    //this.customersList = this.dataService.getCustomers();
    this.dataService.getCustomers(this.searchCriteria)
      .subscribe((response: Customer[]) => {
        this.customersList = response;

      },
      (err: any) => console.log(err),
      () => console.log('getCustomersPage() retrieved customers'));
  }

  pageChanged(page: number) {

    if (this.simpleSearchCriteria.customerCriteria.firstName != "")
      this.getCustomersPage(page, this.simpleSearchCriteria);
    else
      this.getCustomersPage(page, this.searchCriteria);

  }

  getCustomersPage(page: number, searchCriteria: SearchCriteria) {

    this.isLoading = true;

    searchCriteria.pageNumber = page;

    this.dataService.getCustomersPage(searchCriteria)
      .subscribe((response: ICustomerResults) => {

        this.customersList = response.results;

        this.totalRecords = response.totalRecords;
        if (this.isFirstLoad) {

          this.dataService.cacheCustomerSearch(searchCriteria, this.customersList, this.totalRecords);

          this.setPage(1, searchCriteria);
          this.isFirstLoad = false;
        }

        this.isLoading = false;
      },
      (err: any) => console.log(err),
      () => console.log('getCustomersPage retrieved customers'));
  }

  searchCustomers() {

    this.searchCriteria = new SearchCriteria();
    this.simpleSearchCriteria.pageSize = environment.pageSize;

    this.isFirstLoad = true;
    this.simpleSearchCriteria.customerCriteria.searchText = this.customerSearch;

    if (this.customerSearch.indexOf(' ') > 0) {
      this.simpleSearchCriteria.customerCriteria.firstName = this.customerSearch.split(" ")[0];
      this.simpleSearchCriteria.customerCriteria.lastName = this.customerSearch.split(" ")[1];
      this.simpleSearchCriteria.customerCriteria.searchText = "";
    }

    //this.simpleSearchCriteria.customerCriteria.lastName = this.customerSearch;
    //this.simpleSearchCriteria.customerCriteria.companyName = this.customerSearch;
    //this.simpleSearchCriteria.customerCriteria.phone = this.customerSearch;

    this.getCustomersPage(1, this.simpleSearchCriteria);

    //this.dataService.cacheCustomerSearch(this.searchCriteria, this.customersList);
  }

  searchAdvanceCustomers() {

    this.simpleSearchCriteria = new SearchCriteria();
    this.customerSearch = "";
    this.searchCriteria.pageSize = environment.pageSize;

    this.isFirstLoad = true;

    if (this.searchCriteria.customerCriteria.firstName) {
      if (this.searchCriteria.customerCriteria.firstName.indexOf(' ') < 0) {
        this.searchCriteria.customerCriteria.lastName = this.searchCriteria.customerCriteria.firstName;
      }
      else {
        var first: string = this.searchCriteria.customerCriteria.firstName.split(" ")[0];
        var last: string = this.searchCriteria.customerCriteria.firstName.split(" ")[1];
        this.searchCriteria.customerCriteria.firstName = first;
        this.searchCriteria.customerCriteria.lastName = last;
      }
    }
    else {
      this.searchCriteria.customerCriteria.lastName = "";
    }

    this.getCustomersPage(1, this.searchCriteria);

    //this.dataService.cacheCustomerSearch(this.searchCriteria, this.customersList);
  }
  editCustomer(customer: Customer) {

    this.dataService.setSelectedCustomer(customer);
       
    //this.router.navigate(['../', 'customer', 'business-customer']);
  }

  delete(customerId) {
    event.preventDefault();

    this.dataService.deleteCustomer(customerId)
      .subscribe((status: boolean) => {
        if (status) {
          this.toastr.success('The customer was successfully deleted!', 'Success!');
          // this.router.navigate(['/customer/customer-search']);

          var currentIndex: number = 0;
          for (let customer of this.customersList) {

            if (customer.id == customerId)
              this.customersList.splice(currentIndex, 1);

            currentIndex++;
          }

        }
        else {
          this.toastr.error('Unable to delete customer!', 'Error!');
        }
      },
      (err: any) => this.toastr.error(err.json().message));
  }

  createLargeAccount(customer: Customer) {

    this.dataService.setSelectedCustomer(customer);
  }

  ngOnInit() {
   
    this.searchCriteria.pageSize = environment.pageSize;
    this.simpleSearchCriteria.pageSize = environment.pageSize;
    if (this.dataService.customersList$.length > 0) {
      this.isFirstLoad = true;

      this.searchCriteria = this.dataService.searchCriteria$;
      this.customersList = this.dataService.customersList$;
      // this.totalRecords = this.dataService.totalRecords$;

      // this.setPage(1);
    }
  }

}
