import { Component, AfterViewInit } from '@angular/core';
import { ScriptService } from './script.service';
import { AuthenticationService } from './services/authentication.service';
import { TranslateService } from '@ngx-translate/core';
import { DataService } from './data.service';
declare var $: any;
declare var Layout: any;

@Component({
  selector: 'top-menu',
  templateUrl: '/top.menu.component.html'
})

export class TopMenuComponent {
  pageTitle: string = 'Dealer Commissioning';

  scriptService: ScriptService = new ScriptService();
  userName: string = "";
  translate: TranslateService;

  constructor(private dataService: DataService, private authenticationService: AuthenticationService, private currentTranslate: TranslateService) {

    this.translate = currentTranslate;
    this.translate.addLangs(["en", "bg"]);
    this.translate.setDefaultLang('en');

    let browserLang = this.translate.getBrowserLang();
    this.translate.use(browserLang.match(/en|bg/) ? browserLang : 'en');
  }

  selectedItem: string = "welcome";

  changeLang(lang: string): void {

    this.dataService.currentLang$ = lang;
    this.translate.use(lang);

  }

  selectMenu(menuItem: string): void {

    this.selectedItem = menuItem;
    
  }

  logout(): void {



  }

  ngOnInit(): void {
    this.userName = this.authenticationService.loggedUser$.firstName + " " + this.authenticationService.loggedUser$.lastName;
  }
}
