import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/index';
import { ScriptService } from '../../script.service';
import { DataService } from '../../data.service';
import { BasicService } from './basicservice';

declare var $: any;

@Component({
  templateUrl: '/basicservice.search.component.html',
})
export class BasicServiceSearchComponent implements OnInit {
  pageTitle: string = 'Search For Basic Service';
    userSearch: string = "";
    public searchBasicService: string;
  currentProgress: number = 0;
 
  isLoading: boolean = false;

  public basicServicesList: BasicService[] = [];

  constructor(private dataService: DataService,
    public toastr: ToastrService,
    private authenticationService: AuthenticationService,
    private router: Router,
    vcr: ViewContainerRef) {

  }

  getBasicServices() {

    this.isLoading = true;

    this.dataService.getBasicServices()
      .subscribe((response: BasicService[]) => {
        this.basicServicesList = response;
          this.dataService.cacheBasicServiceSearch(this.basicServicesList);
        this.isLoading = false;
      },
        (err: any) => console.log(err),
          () => console.log('getBasicServices() retrieved basic services'));

    
      console.log(this.basicServicesList);
  }


  ngOnInit(): void {
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/tariffs/basicservice-search";   
    this.getBasicServices();

    this.dataService.menuUnselect();
      $("#menuTariffPackages").parent().parent().addClass("open");
      $("#menuTariffPackages").parent().show();
      $("#menuTariffPackages").addClass("active");
      $("#menuTariffPackages").parent().addClass("open");
  }

  editBasicService(basicService: BasicService) {
      this.dataService.setSelectedBasicService(basicService);
  }

  addBasicService() {

    var newBasicService = new BasicService();
      newBasicService.id = 0;

      this.dataService.setSelectedBasicService(newBasicService);
  }

  delete(basicServiceId) {
      event.preventDefault();
      this.dataService.deleteBasicService(basicServiceId)
        .subscribe((status: boolean) => {
          if (status) {
            this.toastr.success('The basic service was successfully deleted!', 'Success!');
            // this.router.navigate(['/customer/customer-search']);

            var currentIndex: number = 0;
            for (let basicService of this.basicServicesList) {

                if (basicService.id == basicServiceId)
                    this.basicServicesList.splice(currentIndex, 1);

              currentIndex++;
            }

          }
          else {
            this.toastr.error('Unable to delete basic service !', 'Error!');
          }
        },
        (err: any) => this.toastr.error(err.json().message));
    }

}
