import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { PasswordModule } from 'primeng/primeng';
import { AuthGuard } from '../guards/auth.guard';
import { AdminGuard } from '../guards/admin.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';
import { CalendarModule } from 'primeng/primeng';
import { FilterPipe } from './filter.pipe';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TimePackageSearchComponent } from './timepackage/timepackage.search.component';
import { ZoneOriginComponent } from './origindestination/origin.component';
import { ZoneDestinationComponent } from './origindestination/destination.component';
import { TimeBandComponent } from './timepackage/timeband.component';
import { TimePackageComponent } from './timepackage/timepackage.component';
import { OriginDestinationSearchComponent } from './origindestination/origin.destination.search.component';
import { ZonePackageSearchComponent } from './zonepackage/zonepackage.search.component';
import { ZonePackageComponent } from './zonepackage/zonepackage.component';
import { ZoneComponent } from './zonepackage/zone.component';
import { TariffModelSearchComponent } from './pricing/tariffmodel.search.component';
import { TariffModelComponent } from './pricing/tariffmodel.component';
import { TariffNodeComponent } from './pricing/tariffnode.component';
import { BasicServiceSearchComponent } from './tariffpackage/basicservice.search.component';
import { BasicServiceComponent } from './tariffpackage/basicservice.component';
import { ProductOfferingSearchComponent } from './tariffpackage/productoffering.search.component';
import { ProductOfferingComponent } from './tariffpackage/productoffering.component';
import { RatePlanSearchComponent } from './tariffpackage/rateplan.search.component';
import { RatePlanComponent } from './tariffpackage/rateplan.component';
import { RatePlanServiceComponent } from './tariffpackage/rateplanservice.component';


@NgModule({
    imports: [

        BrowserModule,
        HttpModule,
        CommonModule,
        FormsModule,
        CalendarModule,
        NgbModule,
        PasswordModule,
        RouterModule.forRoot([
            {
                path: 'tariffs',
                component: SiteLayoutComponent,
                children: [
                    { path: 'timepackage-search', component: TimePackageSearchComponent, canActivate: [AdminGuard] },
                    { path: 'zonepackage-search', component: ZonePackageSearchComponent, canActivate: [AdminGuard] },
                    { path: 'timepackage/:id', component: TimePackageComponent, canActivate: [AdminGuard] },
                    { path: 'timepackage', component: TimePackageComponent, canActivate: [AuthGuard] },
                    { path: 'zonepackage/:id', component: ZonePackageComponent, canActivate: [AdminGuard] },
                    { path: 'zonepackage', component: ZonePackageComponent, canActivate: [AuthGuard] },
                    { path: 'timeband/:id', component: TimeBandComponent, canActivate: [AdminGuard] },
                    { path: 'timeband', component: TimeBandComponent, canActivate: [AuthGuard] },
                    { path: 'zone/:id', component: ZoneComponent, canActivate: [AdminGuard] },
                    { path: 'zone', component: ZoneComponent, canActivate: [AuthGuard] },
                    { path: 'origin-destination', component: OriginDestinationSearchComponent, canActivate: [AdminGuard] },
                    { path: 'zoneorigin/:id', component: ZoneOriginComponent, canActivate: [AdminGuard] },
                    { path: 'zoneorigin', component: ZoneOriginComponent, canActivate: [AuthGuard] },
                    { path: 'zonedestination/:id', component: ZoneDestinationComponent, canActivate: [AdminGuard] },
                    { path: 'zonedestination', component: ZoneDestinationComponent, canActivate: [AuthGuard] },
                    { path: 'tariffmodel-search', component: TariffModelSearchComponent, canActivate: [AdminGuard] },
                    { path: 'tariffmodel/:id', component: TariffModelComponent, canActivate: [AdminGuard] },
                    { path: 'tariffmodel', component: TariffModelComponent, canActivate: [AuthGuard] },
                    { path: 'tariffnode/:id', component: TariffNodeComponent, canActivate: [AdminGuard] },
                    { path: 'tariffnode', component: TariffNodeComponent, canActivate: [AuthGuard] },
                    { path: 'basicservice-search', component: BasicServiceSearchComponent, canActivate: [AdminGuard] },
                    { path: 'basicservice/:id', component: BasicServiceComponent, canActivate: [AdminGuard] },
                    { path: 'basicservice', component: BasicServiceComponent, canActivate: [AuthGuard] },
                    { path: 'productoffering-search', component: ProductOfferingSearchComponent, canActivate: [AdminGuard] },
                    { path: 'productoffering/:id', component: ProductOfferingComponent, canActivate: [AdminGuard] },
                    { path: 'productoffering', component: ProductOfferingComponent, canActivate: [AuthGuard] },
                    { path: 'rateplan-search', component: RatePlanSearchComponent, canActivate: [AdminGuard] },
                    { path: 'rateplan/:id', component: RatePlanComponent, canActivate: [AdminGuard] },
                    { path: 'rateplan', component: RatePlanComponent, canActivate: [AuthGuard] },
                    { path: 'rateplanservice/:id', component: RatePlanServiceComponent, canActivate: [AdminGuard] },
                    { path: 'rateplanservice', component: RatePlanServiceComponent, canActivate: [AuthGuard] },
                ]
            }
        ])
    ],
    declarations: [
        TimePackageSearchComponent,
        TimePackageComponent,
        ZoneOriginComponent,
        ZoneDestinationComponent,
        TimeBandComponent,
        ZonePackageSearchComponent,
        ProductOfferingComponent,
        ZonePackageComponent,
        ZoneComponent,
        TariffModelSearchComponent,
        BasicServiceComponent,
        TariffModelComponent,
        BasicServiceSearchComponent,
        RatePlanSearchComponent,
        TariffNodeComponent,
        RatePlanServiceComponent,
        RatePlanComponent,
        ProductOfferingSearchComponent,
        OriginDestinationSearchComponent,
        FilterPipe

    ],
    providers: [
        
    ]
})
export class TariffsModule { }
