import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/index';
import { ScriptService } from '../../script.service';
import { DataService } from '../../data.service';
import { ZoneOrigin } from './zoneorigin';
import { ZoneDestination } from './zonedestination';

declare var $: any;

@Component({
    templateUrl: '/origin.destination.search.component.html',
})
export class OriginDestinationSearchComponent implements OnInit {
    pageTitle: string = 'Search Origins and Destinations';
    
    public searchZoneOrigins: string;
    public searchZoneDestinations: string;
    currentProgress: number = 0;
    scriptService: ScriptService = new ScriptService();

    isLoadingZoneOrigins: boolean = false;
    isLoadingZoneDestinations: boolean = false;

    public zoneOriginsList: ZoneOrigin[] = [];
    public zoneDestinationsList: ZoneDestination[] = [];

    constructor(private dataService: DataService,
        public toastr: ToastrService,
        private authenticationService: AuthenticationService,
        private router: Router,
        vcr: ViewContainerRef) {
        //this.toastr.setRootViewContainerRef(vcr);
    }

    getZoneOrigins() {
        this.isLoadingZoneOrigins = true;
        this.dataService.getZoneOrigins()
            .subscribe((response: ZoneOrigin[]) => {
                this.zoneOriginsList = response;
                this.dataService.cacheZoneOriginsSearch(this.zoneOriginsList);
                this.isLoadingZoneOrigins = false;
            },
                (err: any) => console.log(err),
                () => console.log('getZoneOrigins() retrieved zone origins'));
        console.log(this.zoneOriginsList);
    }

    getZoneDestinations() {
        this.isLoadingZoneDestinations = true;
        this.dataService.getZoneDestinations()
            .subscribe((response: ZoneDestination[]) => {
                this.zoneDestinationsList = response;
                this.dataService.cacheZoneDestinationsSearch(this.zoneDestinationsList);
                this.isLoadingZoneDestinations = false;
            },
                (err: any) => console.log(err),
                () => console.log('getZoneDestinations() retrieved zone destinations'));
        console.log(this.zoneDestinationsList);
    }

    ngOnInit(): void {
        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/origin-destination";
        this.getZoneOrigins();
        this.getZoneDestinations();
        this.dataService.menuUnselect();
        $("#menuTimeOriginDestination").parent().parent().addClass("open");
        $("#menuTimeOriginDestination").parent().show();
        $("#menuTimeOriginDestination").addClass("active");
    }

    editZoneOrigin(zoneOrigin: ZoneOrigin) {
        this.dataService.setSelectedZoneOrigin(zoneOrigin);
    }

    addZoneOrigin() {

        var newZoneOrigin = new ZoneOrigin();
        newZoneOrigin.id = 0;

        this.dataService.setSelectedZoneOrigin(newZoneOrigin);
    }

    editZoneDestination(zoneDestination: ZoneDestination) {
        this.dataService.setSelectedZoneDestination(zoneDestination);
    }

    addZoneDestination() {

        var newZoneDestination = new ZoneDestination();
        newZoneDestination.id = 0;

        this.dataService.setSelectedZoneDestination(newZoneDestination);
    }

    deleteZoneOrigin(zoneOriginId) {
        event.preventDefault();
        this.dataService.deleteZoneOrigin(zoneOriginId)
          .subscribe((status: boolean) => {
            if (status) {
              this.toastr.success('The zone origin was successfully deleted!', 'Success!');
              // this.router.navigate(['/customer/customer-search']);

              var currentIndex: number = 0;
              for (let zoneOrigin of this.zoneOriginsList) {

                  if (zoneOrigin.id == zoneOriginId)
                      this.zoneOriginsList.splice(currentIndex, 1);

                currentIndex++;
              }

            }
            else {
              this.toastr.error('Unable to delete zone origin!', 'Error!');
            }
          },
              (err: any) => this.toastr.error('Unable to delete zone origin because it is been used!'));
    }

    deleteZoneDestination(zoneDestinationId) {
        event.preventDefault();
        this.dataService.deleteZoneDestination(zoneDestinationId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The zone destination was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let zoneDestination of this.zoneDestinationsList) {

                        if (zoneDestination.id == zoneDestinationId)
                            this.zoneDestinationsList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete zone destination!', 'Error!');
                }
            },
                (err: any) => this.toastr.error('Unable to delete zone destination because it is been used!'));
    }

}
