    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
    }

    cancel(): void {

        this.dataService.setSelectedRatePlan(new RatePlan());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveRatePlan(): void {       
        var isValid = true;        
        this.rateplan.createdByUserId = this.authenticationService.loggedUser$.id;
       
        if (this.rateplan.id == 0 || this.rateplan.id == undefined || this.rateplan.id == null) {

            this.rateplan.id = 0;
      
        }
        if (isValid) {     
            this.rateplan.contractTypeId = this.selectedContractTypeId;
            this.rateplan.currencyId = this.selectedCurrencyId;

            console.log(this.rateplan);
            this.dataService.saveRatePlan(this.rateplan)
                .subscribe((rateplan: RatePlan) => {

                    this.rateplan.id = rateplan.id;
                    
                    this.rateplan = rateplan;
                    this.toastr.success('The rate plan was successfully saved!', 'Success!');
                   
                    this.dataService.setSelectedRatePlan(this.rateplan);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
        
    }

