import { Component, OnInit} from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router} from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { UnitOfMeasure } from '../pricing/unitofmeasure';
import { BasicService } from './basicservice';


declare var $: any;

@Component({
    templateUrl: 'basicservice.component.html'
})
export class BasicServiceComponent implements OnInit {

    pageTitle: string = 'Basic Service';
    public unitsOfMesureList: UnitOfMeasure[] = [];
    
    basicservice: BasicService = new BasicService();
    
    selectedUnitOfMeasureId: number;
    
    currentProgress: number = 0;
    
    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
    }

    cancel(): void {

        this.dataService.setSelectedBasicService(new BasicService());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveBasicService(): void {

        var isValid = true;        
        this.basicservice.createdByUserId = this.authenticationService.loggedUser$.id;
       
        if (this.basicservice.id == 0 || this.basicservice.id == undefined || this.basicservice.id == null) {

            this.basicservice.id = 0;
        }

        if (isValid) {     
            
            this.basicservice.unitOfMeasureId = this.selectedUnitOfMeasureId;
            
            console.log(this.basicservice);
            this.dataService.saveBasicService(this.basicservice)
                .subscribe((basicservice: BasicService) => {

                    this.basicservice.id = basicservice.id;                   
                    this.basicservice = basicservice;
                    this.toastr.success('The basic service was successfully saved!', 'Success!');
                   
                    this.dataService.setSelectedBasicService(this.basicservice);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
        
    }

   

    getUnitsOfMeasure(): void {
        this.dataService.getUnitsOfMeasure()
            .subscribe((response: UnitOfMeasure[]) => {

                this.unitsOfMesureList = response;

                var emptyMeasure = new UnitOfMeasure();
                emptyMeasure.id = null;
                emptyMeasure.name = "";

                this.unitsOfMesureList.unshift(emptyMeasure);

                console.log(this.unitsOfMesureList);
                
                if (this.dataService.getSelectedBasicService().unitOfMeasureId > 0) {

                    this.selectedUnitOfMeasureId = this.dataService.getSelectedBasicService().unitOfMeasureId;
                }
                else {

                    this.selectedUnitOfMeasureId = this.unitsOfMesureList[0].id;
                }
            },
                (err: any) => console.log(err),
                () => console.log('getUnitsOfMeasure retrieved units of measure'));
        
    }

    ngOnInit(): void {

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/basicservice-search";
        
        this.basicservice = this.dataService.getSelectedBasicService();
        
        console.log(this.basicservice);

        if (this.dataService.getSelectedBasicService().id > 0) {

            this.selectedUnitOfMeasureId = this.dataService.getSelectedTariffModel().unitOfMeasureId;
           
        }
        else {
            this.clearBasicService(null);
        }

        this.getUnitsOfMeasure();        
        this.dataService.menuUnselect();
    }
    
    clearBasicService(form) {
        this.basicservice = new BasicService();
        this.basicservice.id = 0;
        
        this.dataService.setSelectedBasicService(new BasicService());

        if (form != null)
            form.reset();
    }

    calcBasicServiceProgress(): number {

        var current: number = 75;

        if (!this.checkIsEmpty(this.basicservice.name))
            current += 25;
        
        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }
}

