import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ICustomerResults } from '../shared/interfaces';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { Contract } from './contract';
import { RatePlan } from './rateplan';
import { AuthenticationService } from '../services/index';
import { ContractService } from './contract.service';
import { DataService } from '../data.service';
import { Customer } from '../customers/customer';
import { SearchCriteria } from '../shared/search.criteria';
import { environment } from '../../environments/environment';
import { PagerService } from '../shared/pager.service';
declare var $: any;

@Component({

  templateUrl: '/contract.component.html'
})
export class ContractComponent implements OnInit {
  pageTitle: string = 'Contract Detail';

  public contractsList: Contract[] = [];
  selectedCustomerFromTemplate: Customer;

  constructor(private dataService: DataService,
    private authenticationService: AuthenticationService,
    public toastr: ToastrService, vcr: ViewContainerRef,
    private router: Router,
    private pagerService: PagerService) {
    //this.toastr.setRootViewContainerRef(vcr);

  }

  contract: Contract = new Contract();
  currentProgress: number = 0;
  public customersList: Customer[] = [];
  public ratePlansList: RatePlan[] = [];
  public searchCriteria: SearchCriteria = new SearchCriteria();
  public simpleSearchCriteria: SearchCriteria = new SearchCriteria();
  public selectedRatePlan: RatePlan;
  totalRecords: number = 0;
  page: number = 1;
  isFirstLoad: boolean = true;
  isLoading: boolean = false;
  customerSearch: string = "";

  contractType: string = "PrePaid";

  contractReady: boolean = false;


  getContract(contractId) {
    //this.customersList = this.dataService.getCustomers();
    this.dataService.getContract(contractId)
      .subscribe((response: Contract) => {
        this.contract = response;

      },
      (err: any) => console.log(err),
      () => console.log('getContract retrieved contract'));
  }

  getRatePlans() {
    //this.customersList = this.dataService.getCustomers();
    this.dataService.getRatePlans()
      .subscribe((response: RatePlan[]) => {
        
        this.ratePlansList = response;
        
      },
      (err: any) => console.log(err),
      () => console.log('getRatePlans retrieved rate plans'));
  }

  signContract(): void {

    this.dataService.printedContract$ = this.contract;
    this.router.navigate(['/', 'contract', 'print-contract']);
  }

  cancel(): void {

    this.dataService.setSelectedContract(new Contract());
    this.router.navigateByUrl(this.dataService.previousPageRoute$);
    //this.clearCustomer(null);
  }
  
  saveContract(form): void {
    var isValid = true;
    this.contract.type = "PrePaid";
    this.contract.createdByUserId = this.authenticationService.loggedUser$.id;

    if (this.selectedCustomerFromTemplate != null) {
      this.contract.customerid = this.selectedCustomerFromTemplate.id;
      if (this.contract.id == 0 || this.contract.id == undefined || this.contract.id == null) {

        this.contract.id = 0;
      }
      else {
        if (this.contract.status == "OnHold") {
          isValid = false;
          this.toastr.error("An existing customer cannot be On Hold!", "Error");
        }
        if (this.contract.status == "Deleted") {
          isValid = false;
          this.toastr.error("An deleted customer cannot be updated!", "Error");
        }
      }

      if (isValid) {
        this.dataService.saveContract(this.contract)
          .subscribe((contract: Contract) => {
            this.contract.id = contract.id;
            this.toastr.success('Creation Successfully Completed!', 'Success!');
            this.dataService.setSelectedContract(new Contract());
          },
          (err: any) => this.toastr.error(err.json().message));
      }
    }
      
  }

  updateFromTemplate($event) {
    this.selectedCustomerFromTemplate = $event;
    //console.log("this.selectedCustomerFromTemplate :" + this.selectedCustomerFromTemplate.firstName);
    if (this.selectedCustomerFromTemplate != null) {
      this.contractReady = true;
    }
    else {
      this.contractReady = false;
    }
  }

  //forContract(customer: Customer) {

  //  if (!customer.isForContract) {

  //    this.contractReady = true;

  //    for (let cust of this.customersList) {

  //      if (cust.id != customer.id) {
  //        cust.isForContract = false;
  //      }

  //    }

  //  }
  //  else {
  //    this.contractReady = false;
  //  }

  //}

  ngOnInit(): void {
    
    this.getRatePlans();
    this.contractType = this.dataService.selectedMenuItem$;
    
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/contract/contract";

    if (this.dataService.getSelectedContract().id > 0) {

      this.dataService.getContract(this.dataService.getSelectedContract().id)
        .subscribe((response: Contract) => {
          this.contract = response;


        },
        (err: any) => console.log(err),
        () => console.log('getContract retrieved contract'));

    }
    else {
      this.clearContract(null);
    }

   

    this.dataService.setSelectedContract(new Contract());
    this.dataService.menuUnselect();


    if (this.contractType == "Pre-Paid") {
      $("#menuPrePaidContract").parent().parent().addClass("open");
      $("#menuPrePaidContract").parent().show();
      $("#menuPrePaidContract").addClass("active");
    }
    else {
      $("#menuPostPaidContract").parent().parent().addClass("open");
      $("#menuPostPaidContract").parent().show();
      $("#menuPostPaidContract").addClass("active");
    }

    
  }


  clearContract(form) {
    this.contract = new Contract();
    this.contract.status = "Active";
    this.contract.title = "Mr";
    this.contract.gender = "MALE";
    this.dataService.setSelectedContract(new Contract());

    if (form != null)
      form.reset();
  }

  calcProgress(): number {

    var current: number = 0;

    if (!this.checkIsEmpty(this.contract.companyName))
      current += 10;
    if (!this.checkIsEmpty(this.contract.firstName))
      current += 10;
    if (!this.checkIsEmpty(this.contract.lastName))
      current += 10;
   
    if (!this.checkIsEmpty(this.contract.gender))
      current += 20;
    if (!this.checkIsEmpty(this.contract.status))
      current += 10;
    if (!this.checkIsEmpty(this.contract.title))
      current += 10;
    if (!this.checkIsEmpty(this.contract.msisdn))
      current += 10;
    if (!this.checkIsEmpty(this.contract.imsi))
      current += 10;
    if (!this.checkIsEmpty(this.contract.contractNo))
      current += 10;
    if (!this.checkIsEmpty(this.contract.ratePlan.id))
      current += 10;

    if (current > 100)
      current = 100;

    this.currentProgress = current;

    if (current > 10)
      $("#contractEditMode").show();
    else
      $("#contractEditMode").hide();

    return current;
  }

  checkIsEmpty(value: any): boolean {

    if (value == "" || value == undefined || value == null)
      return true;

    return false;
  }
}
