import { Component, OnInit} from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { ZoneEntry } from './zoneentry';
import { Zone } from './zone';
import { ZoneOrigin } from '../origindestination/zoneorigin';
import { ZoneDestination } from '../origindestination/zonedestination';

declare var $: any;

@Component({
    templateUrl: 'zone.component.html'
})
export class ZoneComponent implements OnInit {

    pageTitle: string = 'Zone Detail';

    public zoneEntriesList: ZoneEntry[] = [];
    public zoneOriginsList: ZoneOrigin[] = [];
    public zoneDestinationsList: ZoneDestination[] = [];
    zoneentry: ZoneEntry = new ZoneEntry();
    isZoneEntriesLoading: boolean = false;
    zone: Zone = new Zone();
    selectedZoneOriginId: number;
    selectedZoneDestinationId: number;

    currentProgress: number = 0;

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
        //this.toastr.setRootViewContainerRef(vcr);
    }

    cancel(): void {

        this.dataService.setSelectedZone(new Zone());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveZone(form): void {
        var isValid = true;        
        this.zone.createdByUserId = this.authenticationService.loggedUser$.id;

        if (this.zone.id == 0 || this.zone.id == undefined || this.zone.id == null) {

            this.zone.id = 0;           
        }
        else {
            isValid = false
        }

        if (isValid) {
            console.log(this.zone);
            this.dataService.saveZone(this.zone)
                .subscribe((zone: Zone) => {
                    this.zone = zone;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                    this.dataService.setSelectedZone(this.zone);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
    }

    getZoneOrigins(): void {
        this.dataService.getZoneOrigins()
            .subscribe((response: ZoneOrigin[]) => {

                this.zoneOriginsList = response;
                console.log(this.zoneOriginsList);

            },
                (err: any) => console.log(err),
                () => console.log('getZoneOrigins retrieved zone origins'));
        
    }

    getZoneDestinations(): void {
        this.dataService.getZoneDestinations()
            .subscribe((response: ZoneDestination[]) => {

                this.zoneDestinationsList = response;
                console.log(this.zoneDestinationsList);

            },
                (err: any) => console.log(err),
                () => console.log('getZoneDestinations retrieved zone destinations'));
        
    }

    saveZoneEntry(form): void {

        var isValid = true;

        this.zoneentry.createdByUserId = this.authenticationService.loggedUser$.id;

        if (this.zone.id == 0 || this.zone.id == undefined || this.zone.id == null) {

            this.zoneentry.zoneId = 0;
            isValid = false
        }

        if (isValid) {
            console.log(this.zoneentry);
            this.zoneentry.zoneOriginId = this.selectedZoneOriginId;
            this.zoneentry.zoneDestinationId = this.selectedZoneDestinationId;
            console.log(this.selectedZoneOriginId);
            this.dataService.saveZoneEntry(this.zoneentry)
                .subscribe((zoneentry: ZoneEntry) => {
                    console.log(zoneentry);
                    this.zoneentry = zoneentry;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                    this.dataService.setSelectedZoneEntry(this.zoneentry);
                    $('#responsive').modal('toggle');
                    this.loadZoneEntries(this.dataService.getSelectedZone().id);

                },
                    (err: any) => this.toastr.error(err.json().message));
        }
    }

    ngOnInit(): void {

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/zone";
        this.zone = this.dataService.getSelectedZone();
        this.zoneentry = this.dataService.getSelectedZoneEntry();
        console.log(this.zone);

        if (this.dataService.getSelectedZone().id > 0) {

            this.isZoneEntriesLoading = true;
            this.loadZoneEntries(this.dataService.getSelectedZone().id);
            
           
        }
        else {
            this.clearZone(null);
        }

        this.getZoneOrigins();
        this.getZoneDestinations();
        this.dataService.menuUnselect();
    }

    loadZoneEntries(id) {
        this.dataService.getZoneEntries(this.dataService.getSelectedZone().id)
            .subscribe((response: ZoneEntry[]) => {
                this.isZoneEntriesLoading = false;
                this.zoneEntriesList = response;
                console.log(this.zoneEntriesList);

            },
                (err: any) => console.log(err),
                () => console.log('getZoneEntries retrieved zone entries'));
    }

    clearZone(form) {
        this.zone = new Zone();
        this.zone.id = 0;
        this.zone.zonepackageId = this.dataService.getSelectedZonePackage().id;
        this.zoneentry.zoneId = this.dataService.getSelectedZone().id;
        this.dataService.setSelectedZone(new Zone());

        if (form != null)
            form.reset();
    }

    calcZoneProgress(): number {

        var current: number = 0;

        if (!this.checkIsEmpty(this.zone.name))
            current += 100;

        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editZoneEntry(zoneentry: ZoneEntry) {
        console.log(zoneentry);

        this.dataService.setSelectedZoneEntry(zoneentry);
        this.zoneentry = zoneentry;

        this.selectedZoneDestinationId = this.zoneentry.zoneDestinationId;
        this.selectedZoneOriginId = this.zoneentry.zoneOriginId;
        

    }
    addZoneEntry() {
        this.zoneentry = new ZoneEntry();
        this.zoneentry.id = 0;
        this.zoneentry.zoneId = this.dataService.getSelectedZone().id;

        this.dataService.setSelectedZoneEntry(this.zoneentry);
        this.selectedZoneDestinationId = this.zoneDestinationsList[0].id;
        this.selectedZoneOriginId = this.zoneOriginsList[0].id;
    }
    delete(zoneEntryId) {
        event.preventDefault();
        this.dataService.deleteZoneEntry(zoneEntryId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The zone entry was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let zoneentry of this.zoneEntriesList) {

                        if (zoneentry.id == zoneEntryId)
                            this.zoneEntriesList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete zone entry!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.message));
    }
}

