/* Defines the product entity */
import { UserGroup } from './user.group';
export class User {

  id: number;
  createdById: number; 
  firstName: string;
  lastName: string;
  title: string;
  email: string;
  phone: string;
  occupation: string;
  password: string;
  oldPassword: string;
  retypedPassword: string;
  createdByUserId: number;
  active: boolean;
  isForDelete: boolean;
  isAdmin: boolean;
  userGroups: Array<UserGroup> = [];


}
