import { Component, OnInit, } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/index';
import { DataService } from '../../data.service';
import { TariffModel } from './tariffmodel';

declare var $: any;

@Component({
    templateUrl: '/tariffmodel.search.component.html',
})
export class TariffModelSearchComponent implements OnInit {
    pageTitle: string = 'Search Tariff Model';
    userSearch: string = "";
    public searchTariffModel: string;
    currentProgress: number = 0;

    isLoading: boolean = false;

    public tariffModelsList: TariffModel[] = [];

    constructor(private dataService: DataService,
        public toastr: ToastrService,
        private authenticationService: AuthenticationService,
        private router: Router,
        vcr: ViewContainerRef) {
        //this.toastr.setRootViewContainerRef(vcr);
    }

    getTariffModels() {

        this.isLoading = true;

        this.dataService.getTariffModels()
            .subscribe((response: TariffModel[]) => {
                this.tariffModelsList = response;
                this.dataService.cacheTariffModelSearch(this.tariffModelsList);
                this.isLoading = false;
            },
                (err: any) => console.log(err),
                () => console.log('getTariffModels() retrieved tariff models'));


        console.log(this.tariffModelsList);
    }


    ngOnInit(): void {
        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/tariffmodel-search";

        
        this.getTariffModels();

        this.dataService.menuUnselect();
        $("#menuPricing").parent().parent().addClass("open");
        $("#menuPricing").parent().show();
        $("#menuPricing").addClass("active");
    }

    editTariffModel(tariffModel: TariffModel) {
        this.dataService.setSelectedTariffModel(tariffModel);
        
    }

    addTariffModel() {

        var newTariffModel = new TariffModel();
        newTariffModel.id = 0;

        this.dataService.setSelectedTariffModel(newTariffModel);
    }

    delete(tariffModelId) {
        event.preventDefault();
        this.dataService.deleteTariffModel(tariffModelId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The tariff model was successfully deleted!', 'Success!');
                    // this.router.navigate(['/customer/customer-search']);

                    var currentIndex: number = 0;
                    for (let tariffModel of this.tariffModelsList) {

                        if (tariffModel.id == tariffModelId)
                            this.tariffModelsList.splice(currentIndex, 1);

                        currentIndex++;
                    }
                }
                else {
                    this.toastr.error('Unable to delete tariff model!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.json().message));
    }

}
