import { Component, OnInit } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { ZoneOrigin } from './zoneorigin';
import { NumberingPlan } from './numberingplan';
declare var $: any;

@Component({
    templateUrl: 'origin.component.html'
})
export class ZoneOriginComponent implements OnInit {

    pageTitle: string = 'Zone Origin Detail';
    selectedNumberingPlanId: number;
    zoneorigin: ZoneOrigin = new ZoneOrigin();
    public numberingPlanList: NumberingPlan[] = []; 
    currentProgress: number = 0;

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
        //this.toastr.setRootViewContainerRef(vcr);
    }

    cancel(): void {

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveZoneOrigin(form): void {
        var isValid = true;
        
        this.zoneorigin.createdByUserId = this.authenticationService.loggedUser$.id;
        this.zoneorigin.numberingPlanId = this.selectedNumberingPlanId;
        if (this.zoneorigin.id == 0 || this.zoneorigin.id == undefined || this.zoneorigin.id == null) {

            this.zoneorigin.id = 0;            
        }

        if (isValid) {            
            console.log(this.zoneorigin);
            this.dataService.saveZoneOrigin(this.zoneorigin)
                .subscribe((zoneorigin: ZoneOrigin) => {

                    this.zoneorigin = zoneorigin;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');
                },
                    (err: any) => this.toastr.error('Unsuccessfull save. Name and origin field should be unique values!'));
        }
    }  

    ngOnInit(): void {
        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/zoneorigin";      

        this.loadNumberingPlans();

        if (this.dataService.getSelectedZoneOrigin().id > 0) {
            this.zoneorigin = this.dataService.getSelectedZoneOrigin();
            console.log(this.zoneorigin);
            this.selectedNumberingPlanId = this.dataService.getSelectedZoneOrigin().numberingPlanId;
            console.log(this.selectedNumberingPlanId);
        }
        else {
            this.clearZoneOrigin(null);
            
        }
        
        this.dataService.menuUnselect();

    }

    loadNumberingPlans() {
        this.dataService.getNumberingPlans()
            .subscribe((response: NumberingPlan[]) => {
                
                this.numberingPlanList = response;
                this.selectedNumberingPlanId = this.numberingPlanList[0].id;
                console.log(this.numberingPlanList);
            },
                (err: any) => console.log(err),
                () => console.log('getNumberingPlans retrieved numbering plans'));
    }

    clearZoneOrigin(form) {
        this.zoneorigin = new ZoneOrigin();
        this.zoneorigin.id = 0;
        
        this.dataService.setSelectedZoneOrigin(new ZoneOrigin());

        if (form != null)
            form.reset();
    }

    calcZoneOriginProgress(): number {

        var current: number = 0;

        if (!this.checkIsEmpty(this.zoneorigin.name))
            current += 50;
        if (!this.checkIsEmpty(this.zoneorigin.origin))
            current += 50;

        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

}

