import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { NumberingPlan } from './numberingplan';
import { ZoneDestination } from './zonedestination';
declare var $: any;

@Component({
    templateUrl: 'destination.component.html'
})
export class ZoneDestinationComponent implements OnInit {

    pageTitle: string = 'Zone Destination Detail';
    selectedNumberingPlanId: number = 1;
    zonedestination: ZoneDestination = new ZoneDestination();
    public numberingPlanList: NumberingPlan[] = []; 
    currentProgress: number = 0;

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
        //this.toastr.setRootViewContainerRef(vcr);
    }

    cancel(): void {

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveZoneDestination(form): void {
        var isValid = true;

        this.zonedestination.createdByUserId = this.authenticationService.loggedUser$.id;
        this.zonedestination.numberingPlanId = this.selectedNumberingPlanId;
        if (this.zonedestination.id == 0 || this.zonedestination.id == undefined || this.zonedestination.id == null) {

            this.zonedestination.id = 0;
            this.zonedestination.numberingPlanId = 1;
        }

        if (isValid) {
            console.log(this.zonedestination);
            this.dataService.saveZoneDestination(this.zonedestination)
                .subscribe((zonedestination: ZoneDestination) => {

                    this.zonedestination = zonedestination;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                },
                    (err: any) => this.toastr.error("Unsuccessfull save. Name and destination field should be unique values!"));
        }
    }

    

    ngOnInit(): void {
        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/zonedestinaion";
        this.loadNumberingPlans();
        
        if (this.dataService.getSelectedZoneDestination().id > 0) {
            this.zonedestination = this.dataService.getSelectedZoneDestination();
            console.log(this.zonedestination);
            this.selectedNumberingPlanId = this.dataService.getSelectedZoneDestination().numberingPlanId;
            console.log(this.selectedNumberingPlanId);
        }
        else {
            this.clearZoneDestination(null);
            
        }
        this.loadNumberingPlans();
        this.dataService.menuUnselect();

    }

    loadNumberingPlans() {
        this.dataService.getNumberingPlans()
            .subscribe((response: NumberingPlan[]) => {
                
                this.numberingPlanList = response;
                this.selectedNumberingPlanId = this.numberingPlanList[0].id;
                console.log(this.numberingPlanList);
            },
                (err: any) => console.log(err),
                () => console.log('getNumberingPlans retrieved numbering plans'));
    }

    clearZoneDestination(form) {
        this.zonedestination = new ZoneDestination();
        this.zonedestination.id = 0;
        
        this.dataService.setSelectedZoneDestination(new ZoneDestination());

        if (form != null)
            form.reset();
    }

    calcZoneDestinationProgress(): number {

        var current: number = 0;

        if (!this.checkIsEmpty(this.zonedestination.name))
            current += 50;
        if (!this.checkIsEmpty(this.zonedestination.destination))
            current += 50;

        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

}

