import { Component, OnInit} from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../../data.service';
import { AuthenticationService } from '../../services/index';
import { ProductOffering } from './productoffering';
import { ProductOfferingService } from './productofferingservice';
import { BasicService } from './basicservice';
import { TariffModel } from '../pricing/tariffmodel';
import { RatePlanService } from './rateplanservice';
import { RatePlan } from '../../contracts/rateplan';
import { ContractType } from './contracttype';
import { Currency } from './currency';

declare var $: any;

@Component({
    templateUrl: 'rateplanservice.component.html'
})
export class RatePlanServiceComponent implements OnInit {

    pageTitle: string = 'Rate Plan';

    public ratePlanServicesList: RatePlanService[] = [];
    public contractTypesList: ContractType[] = [];
    public currenciesList: Currency[] = [];
    public productOfferingsList: ProductOffering[] = [];
    public basicServicesList: BasicService[] = [];
    public tariffModelsList: TariffModel[] = [];
    isRatePlanServicesLoading: boolean = false;
    //productoffering: ProductOffering = new ProductOffering();
    //productofferingservice: ProductOfferingService = new ProductOfferingService();
    selectedBasicServiceId: number;
    selectedTariffModelId: number;
    selectedContractTypeId: number;
    selectedCurrencyId: number;
    selectedProductOfferingId: number;
    currentProgress: number = 0;
    showTariffModel: boolean = false;
    rateplan: RatePlan = new RatePlan();
    rateplanservice: RatePlanService = new RatePlanService();

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
    }

    cancel(): void {

        this.dataService.setSelectedRatePlan(new RatePlan());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveRatePlan(): void {       
        var isValid = true;        
        this.rateplan.createdByUserId = this.authenticationService.loggedUser$.id;
       
        if (this.rateplan.id == 0 || this.rateplan.id == undefined || this.rateplan.id == null) {

            this.rateplan.id = 0;
      
        }
        if (isValid) {     
            this.rateplan.contractTypeId = this.selectedContractTypeId;
            this.rateplan.currencyId = this.selectedCurrencyId;

            console.log(this.rateplan);
            this.dataService.saveRatePlan(this.rateplan)
                .subscribe((rateplan: RatePlan) => {

                    this.rateplan.id = rateplan.id;
                    
                    this.rateplan = rateplan;
                    this.toastr.success('The rate plan was successfully saved!', 'Success!');
                   
                    this.dataService.setSelectedRatePlan(this.rateplan);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
        
    }

    saveRatePlanService(): void {

        var isValid = true;
        this.rateplanservice.createdByUserId = this.authenticationService.loggedUser$.id;


        if (this.rateplan.id == 0 || this.rateplan.id == undefined || this.rateplan.id == null) {

            this.rateplanservice.id = 0;
        }

        if (isValid) {

            this.rateplanservice.basicServiceId = this.selectedBasicServiceId;
            this.rateplanservice.productOfferingId = this.selectedProductOfferingId;

            let selectedService = this.basicServicesList.find(i => i.id === this.selectedBasicServiceId);
            if (selectedService.usageInd) {
                this.rateplanservice.tariffModelId = this.selectedTariffModelId;
            }
            else {
                this.rateplanservice.tariffModelId = 0;
            }
                        
            console.log(this.rateplanservice);
            this.dataService.saveRatePlanService(this.rateplanservice)
                .subscribe((rateplanservice: RatePlanService) => {

                    this.rateplanservice.id = rateplanservice.id;
                   
                    this.rateplanservice = rateplanservice;
                    this.toastr.success('The rate plan service was successfully saved!', 'Success!');
                    this.loadRatePlanServices();
                    this.dataService.setSelectedRatePlanServices(this.rateplanservice);
                    $('#responsive').modal('toggle');
                },
                    (err: any) => this.toastr.error(err.json().message));
        }

    }
   
    getContractTypes(): void {
        this.dataService.getContractTypes()
            .subscribe((response: ContractType[]) => {

                this.contractTypesList = response;
                console.log(this.contractTypesList);
                                
                this.selectedContractTypeId = this.contractTypesList[0].id;
                
            },
                (err: any) => console.log(err),
                () => console.log('getContractTypes retrieved contract types'));
        
    }

    getCurrencies(): void {
        this.dataService.getCurrencies()
            .subscribe((response: Currency[]) => {

                this.currenciesList = response;
                console.log(this.currenciesList);

                this.selectedCurrencyId = this.currenciesList[0].id;

            },
                (err: any) => console.log(err),
                () => console.log('getCurrencies retrieved currencies'));

    }

    getProductOffering(): void {
        this.dataService.getProductOfferings()
            .subscribe((response: ProductOffering[]) => {

                this.productOfferingsList = response;
                console.log(this.productOfferingsList);

                this.selectedProductOfferingId = this.productOfferingsList[0].id;

            },
                (err: any) => console.log(err),
                () => console.log('getProductOfferings retrieved product offerings'));

    }

    getBasicServices(): void {
        this.dataService.getBasicServices()
            .subscribe((response: BasicService[]) => {

                this.basicServicesList = response;
                console.log(this.basicServicesList);

                this.selectedBasicServiceId = this.basicServicesList[0].id;

            },
                (err: any) => console.log(err),
                () => console.log('getBasicServices retrieved basic services '));

    }

    getTariffModels(): void {
        this.dataService.getTariffModels()
            .subscribe((response: TariffModel[]) => {

                this.tariffModelsList = response;
                console.log(this.tariffModelsList);

                this.selectedTariffModelId = this.tariffModelsList[0].id;

            },
                (err: any) => console.log(err),
                () => console.log('getTariffModels retrieved tariff models '));

    }

    ngOnInit(): void {

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/rateplanservice";
        
        this.rateplan = this.dataService.getSelectedRatePlan();
        
        console.log(this.rateplan);

        if (this.dataService.getSelectedRatePlan().id > 0) {

            this.isRatePlanServicesLoading = true;
            this.loadRatePlanServices();
            
        }
        else {
            this.clearRatePlan(null);
        }
    
        this.getContractTypes();
        this.getCurrencies();
        this.getProductOffering();
        this.getBasicServices();
        this.getTariffModels();
        this.dataService.menuUnselect();
    }

    loadRatePlanServices() {
        this.dataService.getRatePlanService(this.dataService.getSelectedRatePlan().id, this.dataService.getSelectedProductOffering().id)
            .subscribe((response: RatePlanService[]) => {
                this.isRatePlanServicesLoading = false;
                this.ratePlanServicesList = response;
                console.log(this.ratePlanServicesList);

            },
                (err: any) => console.log(err),
                () => console.log('getRatePlanService retrieved rate plan services '));
    }

    clearRatePlan(form) {
        this.rateplan = new RatePlan();
        this.rateplan.id = 0;
        
        this.dataService.setSelectedRatePlan(new RatePlan());

        if (form != null)
            form.reset();
    }

    calcRatePlanProgress(): number {

        var current: number = 75;

        if (!this.checkIsEmpty(this.rateplan.ratePlanName))
            current += 25;
        
        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editRatePlanService(rateplanservice: RatePlanService) {
        console.log(rateplanservice);
        //this.getProductOffering();
        //this.getBasicServices();
        this.dataService.setSelectedRatePlanServices(rateplanservice);
        this.rateplanservice = rateplanservice;

        this.selectedProductOfferingId = rateplanservice.productOfferingId;
        this.selectedBasicServiceId = rateplanservice.basicServiceId;      

        this.selectedTariffModelId = rateplanservice.tariffModelId;
        //this.selectedCurrencyId = rateplan.currencyId;

        let selectedService = this.basicServicesList.find(i => i.id === this.selectedBasicServiceId);

        if (selectedService.usageInd)
            this.showTariffModel = true;
        else
            this.showTariffModel = false;
      
        this.rateplanservice = { ...rateplanservice }
    }
    addRatePlanService() {
        this.rateplanservice = new RatePlanService();
        this.rateplanservice.id = 0;
        this.rateplanservice.ratePlanId = this.dataService.getSelectedRatePlan().id;

        this.dataService.setSelectedRatePlanServices(this.rateplanservice);
        this.selectedBasicServiceId = this.basicServicesList[0].id;
        this.selectedProductOfferingId = this.productOfferingsList[0].id;
        this.selectedTariffModelId = this.tariffModelsList[0].id;
    }

    delete(ratePlanServiceId) {
        event.preventDefault();
        this.dataService.deleteRatePlanService(ratePlanServiceId)
            .subscribe((status: boolean) => {
                if (status) {
                    this.toastr.success('The rate plan service was successfully deleted!', 'Success!');
                    
                    var currentIndex: number = 0;
                    for (let rateplanservice of this.ratePlanServicesList) {

                        if (rateplanservice.id == ratePlanServiceId)
                            this.ratePlanServicesList.splice(currentIndex, 1);

                        currentIndex++;
                    }

                }
                else {
                    this.toastr.error('Unable to delete rate plan service!', 'Error!');
                }
            },
                (err: any) => this.toastr.error(err.message));
    }

    onBasicServiceSelected(event: any) {


        let selectedService = this.basicServicesList.find(i => i.id === this.selectedBasicServiceId);
        if (selectedService.usageInd)
            this.showTariffModel = true;
        else {
            this.showTariffModel = false;

        }

    }

}

